export const WuDisplay: React.FC<
  React.HTMLAttributes<HTMLHeadingElement> & {
    size?: 'md' | 'lg'
    as?: 'h1' | 'h2' | 'h3' | 'h4' | 'div'
  }
> = props => {
  const {size = 'lg', className, children, ...rest} = props
  const sizeToClass = {
    md: 'wu-display-2',
    lg: 'wu-display-1',
  }
  const Tag = props.as || 'h1'
  return (
    <Tag {...rest} className={cn(sizeToClass[size], className)}>
      {children}
    </Tag>
  )
}
type IIcons = (typeof ICON_CLASSES)[number]
export const WuIcon: React.FC<
  React.BaseHTMLAttributes<HTMLBaseElement> & {
    icon: IIcons
  }
> = props => {
  const {icon, className, ...rest} = props
  return (
    <span className={cn(icon, className)} role="presentation" {...rest}></span>
  )
}
