import {DropdownMenu, DropdownMenuContent} from '@/base/ui/dropdownMenu'
import {ScrollArea} from '@/base/ui/scrollArea'
import {cn, getLabelPositionClass} from '@/lib/utils'
import React, {
  useCallback,
  useEffect,
  useLayoutEffect,
  useMemo,
  useRef,
  useState,
} from 'react'
import {WuLabel} from '../formGroup'
import {WuVirtualScroll} from '../virtualScroll'
import {IWuSelectItem, IWuSelectOption} from './types/IWuSelectOptions'
import {WuSelectTrigger} from './ui/_trigger'
import {RenderItems} from './utils/renderItems'
import {useOptionDestructure} from './utils/useOptionDestructure'
import {useResizeContent} from './utils/useResizeContent'

export interface IWuSelectProps<T>
  extends Omit<
    React.SelectHTMLAttributes<HTMLSelectElement>,
    'value' | 'defaultValue' | 'onSelect'
  > {
  defaultValue?: T | T[] | null
  label?: string
  value?: T | T[] | null
  data: T[]
  accessorKey: {value: string; label: string}
  onSelect?: (value: T | T[]) => void
  multiple?: boolean
  variant?: 'flat' | 'outlined'
  dir?: 'ltr' | 'rtl'
  placeholder?: string
  virtualizedThreshold?: number
  maxHeight?: number
  hasGroup?: boolean
  Header?: React.ReactNode
  CustomTrigger?: React.ReactNode
  selectAll?: {
    enable: boolean
    label?: string
    includeDisabled?: boolean
    triggerText?: string
  }
  labelPosition?: 'left' | 'top' | 'right'
}

const DEFAULT_VIRTUALIZED_THRESHOLD = 100
const DEFAULT_MAX_HEIGHT = 400
const DEFAULT_ITEM_HEIGHT = 32
const DEFAULT_SEPARATOR_HEIGHT = 9
const DEFAULT_GROUP_HEIGHT = 32

export const WuSelect = <T,>({
  data,
  onSelect,
  multiple = false,
  defaultValue,
  value: controlledValue,
  accessorKey,
  variant = 'flat',
  dir = 'ltr',
  placeholder = 'Select...',
  label,
  virtualizedThreshold = DEFAULT_VIRTUALIZED_THRESHOLD,
  maxHeight = DEFAULT_MAX_HEIGHT,
  hasGroup = false,
  Header,
  CustomTrigger,
  selectAll,
  disabled,
  labelPosition = 'top',
  ...rest
}: IWuSelectProps<T>): React.JSX.Element => {
  const [selectedValue, setSelectedValue] = useState<T | T[] | null>()
  const parentRef = useRef<HTMLDivElement>(null)
  const labelRef = useRef<HTMLDivElement>(null)

  const [isCheckedAll, setIsCheckedAll] = useState(false)
  const [isPartial, setIsPartial] = useState(false)

  const [labelWidth, setLabelWidth] = useState(0)

  useLayoutEffect(() => {
    if (labelPosition === 'left' && labelRef.current) {
      setLabelWidth(labelRef.current.offsetWidth + 8)
    } else {
      setLabelWidth(0)
    }
  }, [labelPosition])

  const contentWidth = useResizeContent(parentRef, labelWidth)

  const [optionsCount, separatorsCount, groupsCount, enabledOptions = []] =
    hasGroup || (selectAll && !selectAll.includeDisabled)
      ? useOptionDestructure(data as IWuSelectOption<T>[])
      : [data.length, 0, 0]

  const modifiedData = useMemo(() => {
    if (!multiple) {
      return data
    }
    if (selectAll?.enable && data.length > 0) {
      return [{type: 'all'} as IWuSelectItem<T>, ...data]
    }
    return data
  }, [data, selectAll, multiple])

  const handleOnSelect = useCallback(
    (selectedOption: IWuSelectItem<T>) => {
      const newValue = selectedOption[accessorKey.value as keyof T] as T

      if (multiple) {
        setSelectedValue((prevValue = []): T | T[] => {
          const currentSelection = prevValue as T[]
          const isSelected = currentSelection.some(
            item => item[accessorKey.value as keyof T] === newValue,
          )

          const updatedSelection = isSelected
            ? currentSelection.filter(
                item => item[accessorKey.value as keyof T] !== newValue,
              )
            : [...currentSelection, selectedOption]

          onSelect?.(updatedSelection as T | T[])
          return updatedSelection as T | T[]
        })
      } else {
        const newSelection = selectedOption as T
        setSelectedValue(newSelection)
        onSelect?.(newSelection)
      }
    },
    [multiple, accessorKey.value, onSelect],
  )

  useEffect(() => {
    if (defaultValue && controlledValue) {
      throw new Error('defaultValue and value cannot be used simultaneously')
    }
    if (!defaultValue && !controlledValue) setSelectedValue(undefined)
    if (defaultValue !== undefined) setSelectedValue(defaultValue)
    if (controlledValue !== undefined) setSelectedValue(controlledValue)
  }, [defaultValue, controlledValue])

  const scrollHeight = useMemo(
    () =>
      Math.min(
        maxHeight,
        optionsCount * DEFAULT_ITEM_HEIGHT +
          separatorsCount * DEFAULT_SEPARATOR_HEIGHT +
          groupsCount * DEFAULT_GROUP_HEIGHT,
      ),
    [maxHeight, optionsCount, separatorsCount, groupsCount],
  )

  const renderVirtualized = modifiedData.length > virtualizedThreshold

  useEffect(() => {
    if (!multiple) return

    const isAllSelected = selectAll?.includeDisabled
      ? (selectedValue as T[])?.length === data.length
      : (selectedValue as T[])?.length === enabledOptions.length

    setIsCheckedAll(isAllSelected)

    const isPartialSelected =
      (selectedValue as T[])?.length > 0 && !isAllSelected
    setIsPartial(isPartialSelected)
  }, [selectedValue, selectAll])

  const handleSelectAll = useCallback((): void => {
    if (!multiple) return

    const newSelectedOptions = isCheckedAll
      ? []
      : selectAll?.includeDisabled
        ? data
        : enabledOptions

    setSelectedValue(newSelectedOptions)
    onSelect?.(newSelectedOptions)
  }, [isCheckedAll, multiple, data, enabledOptions, onSelect, selectAll])

  const selectContainerClass = getLabelPositionClass(labelPosition)

  return (
    <div
      className={cn(
        selectContainerClass,
        (labelPosition === 'right' || labelPosition === 'left') &&
          'items-center',
        'flex wu-select-container',
      )}
      ref={parentRef}
      dir={dir}
    >
      <DropdownMenu>
        <WuSelectTrigger
          value={selectedValue}
          multiple={multiple}
          variant={variant}
          dir={dir}
          placeholder={placeholder}
          accessorKey={accessorKey}
          customTriggerChildren={CustomTrigger}
          id={rest.id}
          disabled={disabled}
          selectAll={selectAll}
          isCheckedAll={isCheckedAll}
          {...rest}
        />
        <DropdownMenuContent
          className="border-blue-p wu-select-content"
          role="listbox"
          sideOffset={1}
          style={{width: `${contentWidth}px`}}
          id={rest.id}
        >
          {Header}
          {renderVirtualized ? (
            <WuVirtualScroll
              list={modifiedData}
              containerHeight={maxHeight}
              callback={(_, option) => (
                <RenderItems
                  option={option as IWuSelectItem<T>}
                  accessorKey={accessorKey}
                  multiple={multiple}
                  onSelect={handleOnSelect}
                  value={selectedValue}
                  dir={dir}
                  selectAll={selectAll}
                  handleSelectAll={handleSelectAll}
                  isCheckedAll={isCheckedAll}
                  isPartialSelected={isPartial}
                />
              )}
              dir={dir}
            />
          ) : (
            <ScrollArea style={{height: scrollHeight}}>
              {modifiedData.map((option, index) => (
                <RenderItems
                  key={`${index}`}
                  option={option as IWuSelectItem<T>}
                  accessorKey={accessorKey}
                  multiple={multiple}
                  onSelect={handleOnSelect}
                  value={selectedValue}
                  selectAll={selectAll}
                  handleSelectAll={handleSelectAll}
                  isCheckedAll={isCheckedAll}
                  dir={dir}
                  isPartialSelected={isPartial}
                />
              ))}
            </ScrollArea>
          )}
        </DropdownMenuContent>
      </DropdownMenu>
      {label && (
        <div ref={labelRef} dir={dir}>
          <WuLabel
            className="truncate max-w-[120px] min-w-fit wu-select-label"
            htmlFor={rest.id}
          >
            {label}
          </WuLabel>
        </div>
      )}
    </div>
  )
}
