import {Slot} from '@radix-ui/react-slot'
import {cva, type VariantProps} from 'class-variance-authority'
import * as React from 'react'

import {cn} from '@/lib/utils'

export const buttonVariants = cva(
  'inline-flex items-center justify-center whitespace-nowrap rounded-sm text-sm font-normal ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-blue-focus focus-visible:ring-offset-2 disabled:cursor-not-allowed leading-4 disabled:bg-gray-20 disabled:text-gray-subtle disabled:opacity-80 disabled:ring-gray-subtle leading-[1px]',
  {
    variants: {
      size: {
        md: 'px-4 py-2 min-w-[104px] h-8',
        sm: 'px-4 py-1 min-w-[104px] h-6',
        mobile: 'px-4 py-2 min-w-[104px] h-10',
      },
      color: {
        primary:
          'bg-blue-p hover:bg-blue-pHover text-blue-p ring-1 ring-blue-p hover:text-blue-q hover:ring-blue-pHover',
        upgrade:
          'bg-orange hover:bg-orange-deep text-orange hover:text-orange-deep ring-1 ring-orange hover:ring-orange-deep',
        error:
          'bg-red-500 hover:bg-red-700 text-red-500 hover:text-red-700 ring-1 ring-red-500 hover:ring-red-700',
      },
      variant: {
        primary: 'text-white ring-0 hover:text-white',
        secondary: 'bg-transparent ring-0 hover:bg-gray-20 ',
        outline: 'hover:text-white bg-white disabled:bg-transparent',
        rounded: 'text-white ring-0 rounded-full hover:text-white',
        link: 'hover:underline bg-transparent hover:bg-transparent ring-0 underline-offset-2 min-w-[auto] px-2 disabled:bg-transparent',
        iconOnly:
          'rounded p-2 text-xl h-8 w-8 leading-5 text-gray-lead hover:text-blue-q hover:bg-gray-20 ring-0 bg-transparent min-w-[auto] disabled:grayscale disabled:opacity-80 rounded-sm focus-visible:ring-offset-0 focus-visible:bg-gray-20 aspect-square',
      },
    },
    defaultVariants: {
      variant: 'primary',
      size: 'md',
      color: 'primary',
    },
  },
)

export interface IButtonProps
  extends React.ButtonHTMLAttributes<HTMLButtonElement>,
    VariantProps<typeof buttonVariants> {
  asChild?: boolean
  color?: 'primary' | 'upgrade' | 'error'
}

const Button = React.forwardRef<HTMLButtonElement, IButtonProps>(
  ({className, variant, size, color, asChild = false, ...props}, ref) => {
    const Comp = asChild ? Slot : 'button'
    return (
      <Comp
        className={cn(buttonVariants({size, color, variant, className}))}
        ref={ref}
        {...props}
      />
    )
  },
)
Button.displayName = 'Button'

export {Button}
