import {Label} from '@/base/ui/label'
import {cn, randomId} from '@/lib/utils'
import {cva} from 'class-variance-authority'
import React from 'react'
import {Switch} from '../../base/ui/switch'

export interface IWuToggleProps
  extends Omit<React.HTMLAttributes<HTMLButtonElement>, 'onChange'> {
  disabled?: boolean
  checked?: boolean
  label?: string
  labelPosition?: 'left' | 'right' | 'top'
  onChange?: (e: boolean) => void
}

export const WuToggle = React.forwardRef<HTMLButtonElement, IWuToggleProps>(
  (props, ref): React.JSX.Element => {
    const {
      disabled,
      checked,
      label,
      labelPosition = 'right',
      id = randomId(),
      onChange,
      className,
      ...restProps
    } = props

    return (
      <div
        className={cn(
          labelPositionClasses({labelPosition}),
          'wu-toggle-container',
        )}
        dir={restProps.dir}
      >
        <Switch
          id={id}
          {...restProps}
          ref={ref}
          defaultChecked={checked}
          disabled={disabled}
          onCheckedChange={onChange}
          className={cn('peer', className)}
          dir={restProps.dir}
        />
        <Label
          htmlFor={id}
          className={cn(
            disabled ? 'cursor-not-allowed' : 'cursor-pointer',
            'wu-toggle-label',
          )}
          dir={restProps.dir}
        >
          {label}
        </Label>
      </div>
    )
  },
)

WuToggle.displayName = 'WuToggle'

const labelPositionClasses = cva('flex items-center gap-3 w-fit', {
  variants: {
    labelPosition: {
      right: 'flex-row',
      left: 'flex-row-reverse',
      top: 'flex-col-reverse items-baseline',
    },
  },
})
