import {TableBody, TableCell, TableRow} from '@/base/ui/table'
import {cn} from '@/lib/utils'
import {flexRender, Table} from '@tanstack/react-table'
import React, {useState} from 'react'

export const WuTableBody = <T,>({
  table,
  size,
  NoDataContent,
}: {
  table: Table<T>
  size?: 'compact' | 'default'
  NoDataContent?: React.ReactNode
}): React.JSX.Element => {
  const [hoveredRowId, setHoveredRowId] = useState<string | null>(null)

  return (
    <TableBody>
      {table.getRowModel().rows?.length ? (
        table.getRowModel().rows.map(row => (
          <TableRow
            key={row.id}
            data-state={row.getIsSelected() && 'selected'}
            onMouseOver={() => setHoveredRowId(row.id)}
            onMouseOut={() => setHoveredRowId(null)}
            className={cn(
              'transition-colors border-b border-black/[12%]',
              row.getIsSelected()
                ? hoveredRowId === row.id
                  ? 'bg-blue-p/[16%]' // Hover + selected
                  : 'bg-blue-p/[8%]'
                : 'hover:bg-black/[2%]',
            )}
          >
            {row.getVisibleCells().map(cell => (
              <TableCell
                key={cell.id}
                className={cn(
                  'py-0 font-light text-gray-lead text-xs',
                  size === 'compact' ? 'h-8' : 'h-[40px]',
                  cell.column.id === 'actions'
                    ? 'opacity-0 transition-opacity duration-100'
                    : '',
                  hoveredRowId === row.id && cell.column.id === 'actions'
                    ? 'opacity-100 visible'
                    : '',
                )}
              >
                {flexRender(cell.column.columnDef.cell, cell.getContext())}
              </TableCell>
            ))}
          </TableRow>
        ))
      ) : (
        <TableRow>
          <TableCell colSpan={table.getAllColumns().length}>
            {NoDataContent}
          </TableCell>
        </TableRow>
      )}
    </TableBody>
  )
}
