import {WuIcon} from '@/components/typography'
import {cn} from '@/lib/utils'
import React, {useEffect, useMemo, useRef, useState} from 'react'
import {useOnClickOutside} from '../hooks/useOnClickOutsideHook'
import useMediaQuery from '../hooks/useWindowSize'
import {
  IWuAppHeaderMenuItem,
  IWuAppHeaderSubMenuItem,
} from '../types/WuAppHeaderMenuItem'
import {iconMap} from '../utils/iconMap'
import './WuAppHeaderMenu.css'

interface IWuAppHeaderMenuProps {
  productCategories: IWuAppHeaderMenuItem[]
  activeProductName?: string
  activeProductColor?: {
    base: string
    hover: string
  }
  licenseUpgradeUrl: string
  dir: 'ltr' | 'rtl'
  homeLink?: string
  expandAll?: boolean
}

enum KeyboardKey {
  ArrowDown = 'ArrowDown',
  ArrowUp = 'ArrowUp',
  Tab = 'Tab',
  Enter = 'Enter',
  Space = ' ',
}

const borderColorLocked = '#ffcf70'
const borderColorCommon = '#c5cce0'
const borderColorHoverOrActive = '#1b3380'

export const WuAppHeaderMenu: React.FC<IWuAppHeaderMenuProps> = ({
  productCategories,
  activeProductName,
  activeProductColor = {base: '#1b87e6', hover: '#54A5EC'},
  licenseUpgradeUrl,
  dir,
  homeLink,
  expandAll,
}) => {
  const buttonRef = useRef<HTMLButtonElement | null>(null)
  const dropdownRef = useRef<HTMLUListElement | null>(null)
  const mobileButtonRef = useRef<HTMLButtonElement | null>(null)
  const mobileButtonRefToggle = useRef<HTMLButtonElement | null>(null)

  const [isDropdownOpen, setDropdownOpen] = useState(false)
  const [openCategory, setOpenCategory] = useState<IWuAppHeaderMenuItem | null>(
    null,
  )
  const [isHoverMode, setHoverMode] = useState(window.innerWidth > 768)
  const [activeProductBgColor, setActiveProductBgColor] = useState(
    activeProductColor.base,
  )
  const [focusItem, setFocusItem] = useState<string>('')
  const [isProductButtonShow, setIsProductButtonShow] = useState<boolean>(false)

  const isMd = useMediaQuery(768)

  useOnClickOutside([dropdownRef, buttonRef], () => {
    setDropdownOpen(false)
    setOpenCategory(null)
  })

  useOnClickOutside(
    [mobileButtonRef, mobileButtonRefToggle, dropdownRef],
    () => {
      setIsProductButtonShow(false)
      if (isProductButtonShow) {
        setDropdownOpen(false)
      }
      setOpenCategory(null)
    },
  )

  useEffect(() => {
    const handleEscKey = (event: KeyboardEvent): void => {
      if (event.key === 'Escape') {
        setDropdownOpen(false)
      }
    }
    document.addEventListener('keydown', handleEscKey)
    return (): void => {
      document.removeEventListener('keydown', handleEscKey)
    }
  }, [isDropdownOpen])

  useEffect(
    () => setActiveProductBgColor(activeProductColor.base),
    [activeProductColor.base],
  )
  useEffect(() => {
    const handleResize = (): void => {
      setHoverMode(window.innerWidth > 375)
    }
    window.addEventListener('resize', handleResize)
    return (): void => window.removeEventListener('resize', handleResize)
  }, [])

  const handleCategoryToggle = (category: IWuAppHeaderMenuItem): void => {
    setOpenCategory(prev => (prev === category ? null : category))
  }

  const handleDropdownToggle: React.MouseEventHandler = event => {
    event.preventDefault()
    event.stopPropagation()
    setDropdownOpen(prev => {
      const nextState = !prev
      if (!nextState) {
        setOpenCategory(null)
      }
      return nextState
    })
  }

  const moveFocus = (
    newIndex: number,
    items: NodeListOf<HTMLElement>,
  ): void => {
    const newItem = items[newIndex]
    if (newItem) {
      newItem.focus()
      const newCategory = productCategories[newIndex] || null
      setOpenCategory(newCategory)
    }
  }

  const handleArrowNavigation = (
    event: React.KeyboardEvent,
    index: number,
    direction: 'up' | 'down',
    items: NodeListOf<HTMLElement>,
  ): void => {
    event.preventDefault()
    const newIndex = direction === 'down' ? index + 1 : index - 1
    moveFocus(newIndex, items)
  }

  const handleTabNavigation = (
    event: React.KeyboardEvent,
    index: number,
    items: NodeListOf<HTMLElement>,
  ): void => {
    if (openCategory?.products) {
      const lastProduct =
        openCategory.products[openCategory.products.length - 1]
      if (lastProduct.name === focusItem) {
        event.preventDefault()
        moveFocus(index + 1, items)
      }
    }
  }

  const handleEnterOrSpace = (
    event: React.KeyboardEvent,
    index: number,
  ): void => {
    event.preventDefault()
    const selectedCategory =
      productCategories[index] === openCategory
        ? null
        : productCategories[index]
    setOpenCategory(selectedCategory)
  }

  const handleKeyDown = (
    event: React.KeyboardEvent<HTMLLIElement | HTMLButtonElement>,
    index: number,
  ): void => {
    const items = document.querySelectorAll(
      '.productCategory',
    ) as NodeListOf<HTMLElement>
    const {key} = event

    switch (key) {
      case KeyboardKey.ArrowDown:
        handleArrowNavigation(event, index, 'down', items)
        break
      case KeyboardKey.ArrowUp:
        handleArrowNavigation(event, index, 'up', items)
        break
      case KeyboardKey.Tab:
        handleTabNavigation(event, index, items)
        break
      case KeyboardKey.Enter:
      case KeyboardKey.Space:
        handleEnterOrSpace(event, index)
        break
      default:
        break
    }
  }

  const toggleMenu = (): void => {
    setIsProductButtonShow(prev => !prev)
    if (isProductButtonShow) {
      setDropdownOpen(false)
    }
  }

  const renderMenuToggleButton = (): React.ReactNode => {
    return (
      <button
        className={cn(
          'relative w-10 h-10 flex flex-col justify-center items-center',
          dir === 'rtl' ? 'mr-4' : ' ml-4',
        )}
        onClick={toggleMenu}
        ref={mobileButtonRefToggle}
      >
        <span
          className={`absolute w-6 h-[3px] bg-white transition-transform duration-300 ${
            isProductButtonShow ? 'rotate-45 translate-y-0' : '-translate-y-2.5'
          }`}
        ></span>
        <span
          className={`absolute w-6 h-[3px] bg-white transition-opacity duration-300 ${
            isProductButtonShow ? 'opacity-0' : 'opacity-100'
          }`}
        ></span>
        <span
          className={`absolute w-6 h-[3px] bg-white transition-transform duration-300 ${
            isProductButtonShow ? '-rotate-45 translate-y-0' : 'translate-y-2.5'
          }`}
        ></span>
      </button>
    )
  }

  const renderMobileProductButton = (): React.ReactNode => {
    return (
      <div
        className={cn(
          'absolute top-11 transition-all duration-300 ease-in-out transform opacity-100 w-[256px] z-10',
          isProductButtonShow
            ? dir === 'rtl'
              ? 'right-0 overflow-hidden'
              : 'translate-x-0 left-0'
            : dir === 'rtl'
              ? 'right-0 w-0'
              : '-translate-x-full',
        )}
        style={{
          backgroundColor: isDropdownOpen
            ? activeProductColor.hover
            : activeProductBgColor,
        }}
      >
        <button
          ref={mobileButtonRef}
          type="button"
          className={cn(
            'text-base leading-[48px] flex items-center justify-center px-2 gap-2 w-full focus:outline-none focus-visible:bg-white/10 wu-switcher-trigger',
            isProductButtonShow ? 'flex' : dir === 'rtl' ? 'hidden' : 'flex',
          )}
          onClick={handleDropdownToggle}
          onKeyDown={e => {
            if (isDropdownOpen && e.key === 'ArrowDown') {
              handleKeyDown(e, -1)
            }
          }}
        >
          {activeProductName}
          <span
            className={cn(
              'wm-arrow-drop-down text-2xl transition-transform duration-300',
              isDropdownOpen && 'rotate-180',
            )}
          ></span>
        </button>
      </div>
    )
  }

  const productButton = (): React.ReactNode => {
    return (
      <button
        ref={buttonRef}
        type="button"
        className={cn(
          'text-base font-normal flex items-center justify-between px-2 gap-2 w-full focus:outline-none focus-visible:bg-white/10 wu-switcher-trigger',
        )}
        onClick={handleDropdownToggle}
        onKeyDown={e => {
          if (isDropdownOpen && e.key === 'ArrowDown') {
            handleKeyDown(e, -1)
          }
        }}
      >
        <a
          className="pl-2 pr-0 aspect-auto focus:outline-none focus-visible:bg-white/10 wu-switcher-logo"
          tabIndex={-1}
          href={homeLink || '/'}
        >
          <WuIcon icon="wc-logo" className="text-xl leading-4 relative top-1" />
        </a>
        {activeProductName}
        <span
          className={cn(
            'wm-arrow-drop-down text-2xl transition-transform duration-300',
            isDropdownOpen && 'rotate-180',
            dir === 'rtl' ? 'mr-auto' : 'ml-auto',
          )}
        ></span>
      </button>
    )
  }

  const renderDropdownMenu = (): React.ReactNode => {
    return (
      <ul
        className={cn(
          'productDropdownMenu',
          isDropdownOpen && 'productDropdownMenuOpen',
          dir === 'rtl' && 'rtlDropdownMenu',
          'wu-switcher-container',
        )}
        ref={dropdownRef}
        dir={dir}
      >
        {productCategories.map((category, index) => {
          const isLocked = !category.products

          return (
            <li
              key={category.name}
              className={cn(
                'productCategory focus:outline-none focus-visible:outline-none h-16 wu-category-item',
                openCategory === category && 'open',
                expandAll && 'open',
              )}
              onMouseEnter={() => isHoverMode && setOpenCategory(category)}
              onMouseLeave={() => isHoverMode && setOpenCategory(null)}
              onClick={() => !isHoverMode && handleCategoryToggle(category)}
              aria-label={category.name}
              dir={dir}
              onKeyDown={e => handleKeyDown(e, index)}
              tabIndex={0}
            >
              {isLocked ? (
                <a className="p-0 m-0 no-underline" href={licenseUpgradeUrl}>
                  {renderCategoryDetails(category)}
                </a>
              ) : (
                renderCategoryDetails(category)
              )}
              <ul
                className={cn('productList', dir === 'rtl' && 'rtlProductList')}
                dir={dir}
              >
                {renderProductList(category, category.name)}
              </ul>
            </li>
          )
        })}
      </ul>
    )
  }

  const renderProductList = (
    category: IWuAppHeaderMenuItem,
    currentProduct: string,
  ): React.ReactNode => {
    const {products = [], active} = category

    const sortedActiveProducts = useMemo(
      () =>
        products
          .filter(item => item.active)
          .sort((a, b) => (a.order || 100) - (b.order || 100)),
      [products],
    )

    const inactiveProducts = useMemo(
      () => products.filter(item => !item.active),
      [products],
    )

    const renderProductItems = (
      items: IWuAppHeaderSubMenuItem[],
    ): React.ReactNode =>
      items.map((item, i) => (
        <_productItemCommon
          key={i}
          product={item}
          currentProduct={currentProduct}
          activeProductName={activeProductName}
          dir={dir}
          focusItem={focusItem}
          setFocusItem={setFocusItem}
        />
      ))

    if (!active) {
      return (
        <li
          className={cn(
            'productListItem learnMore focus:outline-none focus-visible:outline-none wu-product-item-inactive !bg-[#f5f5f5] hover:!bg-[#eeeeee]',
            focusItem === 'learn' && 'activeProduct',
          )}
          tabIndex={0}
          onFocus={() => {
            setFocusItem('learn')
          }}
          onBlur={() => setFocusItem('')}
        >
          <div
            className={cn('topBorder', dir === 'rtl' && 'topBorderRtl')}
            style={{backgroundColor: borderColorLocked}}
          />
          <a
            className="productLink"
            href="https://www.questionpro.com/a/showUpgradeUser.do"
            tabIndex={-1}
          >
            <span className="learnMoreText">Learn More</span>
          </a>
        </li>
      )
    }

    const [lockedBg, setLockedBg] = useState(borderColorLocked)
    return (
      <>
        {renderProductItems(sortedActiveProducts)}

        {inactiveProducts.length > 0 && (
          <li
            className={cn(
              'productListItem focus:outline-none focus-visible:outline-none !bg-[#f5f5f5] hover:!bg-[#eeeeee] group',
            )}
          >
            <div
              className={cn('topBorder', dir === 'rtl' && 'topBorderRtl')}
              style={{backgroundColor: lockedBg}}
              onMouseOver={() => setLockedBg('#f5a300')}
              onMouseOut={() => setLockedBg(borderColorLocked)}
            />
            <a
              href="https://www.questionpro.com/a/showUpgradeUser.do"
              className="productLink flex flex-col items-center justify-center py-2 min-w-[88px]"
            >
              <span className="text-base transition-colors wm-lock text-orange-soft group-hover:text-orange-deep"></span>
              <span className="text-[10px] leading-[10px] text-center pt-1 !text-orange-deep">
                Upgrade to <br /> unlock
              </span>
            </a>
          </li>
        )}

        {renderProductItems(inactiveProducts)}
      </>
    )
  }

  const renderCategoryDetails = (
    category: IWuAppHeaderMenuItem,
  ): React.ReactNode => {
    const categoryBackground = category.active ? '#c5cce0' : '#f0c36a'
    const arrowIconClass = cn(
      'wm-arrow-forward-ios text-xs tracking-widest text-muted-foreground mr-2.5 group-hover:hidden text-blue-q',
      dir === 'rtl' && 'rotate-180 ml-2.5 text-blue-q group-hover:hidden',
      openCategory?.name === name && 'hidden',
      'relative top-[5px]',
    )

    const lockIconClass = cn(
      'wm-lock text-orange-soft text-base mr-2.5 group-hover:hidden',
      dir === 'rtl' && 'ml-2.5 group-hover:hidden',
      openCategory?.name === name && 'hidden',
      'relative top-1',
    )

    return (
      <div
        className="productCategoryDetails group focus:outline-none focus-visible:outline-none"
        dir={dir}
      >
        <div
          className="productCategoryBorderTop"
          style={{backgroundColor: categoryBackground}}
        />
        <div className="flex items-center px-1 relative top-1">
          <div className="productCategoryIcon">
            <img
              src={category.logo}
              alt={category.name}
              width="32px"
              height="32px"
            />
          </div>
          <span className="text-sm leading-5 text-blue-q reletive top-[1px]">
            {category.name}
          </span>
        </div>

        {category.active ? (
          <span className={arrowIconClass}></span>
        ) : (
          <span className={lockIconClass}></span>
        )}
      </div>
    )
  }

  return (
    <div
      className={cn(
        'inline-flex items-center justify-between  relative z-auto h-12',
      )}
    >
      {!isMd ? (
        <div
          className="relative flex items-center justify-start h-full w-[256px]"
          dir={dir}
          style={{
            backgroundColor: isDropdownOpen
              ? activeProductColor.hover
              : activeProductBgColor,
          }}
          onMouseOver={() => setActiveProductBgColor(activeProductColor.hover)}
          onMouseOut={() => setActiveProductBgColor(activeProductColor.base)}
        >
          {productButton()}
          {renderDropdownMenu()}
        </div>
      ) : (
        <div className="relative flex">
          {renderMenuToggleButton()}
          {renderMobileProductButton()}
          <div
            className={cn(
              'absolute top-[92px] left-0 text-[18px]',
              dir === 'rtl' && 'right-[256px] left-auto',
            )}
          >
            {renderDropdownMenu()}
          </div>
        </div>
      )}
    </div>
  )
}

const _productItemCommon: React.FC<{
  product: IWuAppHeaderSubMenuItem
  currentProduct?: string
  activeProductName?: string
  dir?: 'ltr' | 'rtl'
  focusItem: string
  setFocusItem: (item: string) => void
}> = ({
  product,
  currentProduct,
  activeProductName,
  dir,
  focusItem,
  setFocusItem,
}) => {
  const [hover, setHover] = useState(product.name === currentProduct)
  const isActive = product.name === activeProductName
  const isCurrent = product.name === currentProduct
  const isLocked = !product.active

  const textClass = cn(
    'font-normal text-xs text-gray-lead text-center',
    isCurrent && 'text-blue-q font-normal',
    !product.active && 'text-gray-subtle',
    hover && 'font-normal',
    product.active && hover && 'text-blue-q',
  )

  const iconClass = cn(
    'text-blue-p text-base',
    isCurrent && 'text-blue-q',
    !product.active && 'text-gray-subtle',
    product.active && hover && 'text-blue-q',
  )

  const backgroundColor =
    hover || focusItem === product.name
      ? borderColorHoverOrActive
      : isActive
        ? borderColorHoverOrActive
        : borderColorCommon

  return (
    <li
      key={product.name}
      className={cn(
        'productListItem focus-visible:outline-none focus:outline-none wu-sub-product-list-item',
        (isActive || hover) && 'activeProduct',
        product.name.length > 'Instant Answers'.length &&
          window.innerWidth > 768 &&
          'w-[130px] min-w-[130px]',
        focusItem === product.name && 'activeProduct',
        'wu-product-item',
      )}
      onMouseOver={() => setHover(true)}
      onMouseOut={() => setHover(false)}
      aria-label={product.name}
      aria-disabled={isLocked}
      dir={dir}
      onKeyDown={event => {
        if (event.key === 'Enter') {
          event.preventDefault()
          const link = event.currentTarget.querySelector('a')
          if (link) {
            link.click()
          }
        }
      }}
      onFocus={() => {
        setFocusItem(product.name)
      }}
      onBlur={() => setFocusItem('')}
    >
      <div
        className={cn('topBorder', dir === 'rtl' && 'topBorderRtl')}
        style={{
          backgroundColor: isLocked ? borderColorLocked : backgroundColor,
        }}
        onKeyDown={event => {
          if (event.key === 'Enter') {
            event.preventDefault()
            document.getElementById(`link-${product.name}`)?.click()
          }
        }}
      />
      <a
        className="productLink focus:outline-none focus-visible:outline-none"
        href={product.link || 'javascript:void(0)'}
        tabIndex={0}
      >
        <span
          className={cn(iconClass, iconMap[product.icon || ''])}
          aria-hidden
        />
        <span className={cn(textClass)}>{product.name}</span>
      </a>
    </li>
  )
}
