import {DropdownMenuGroup, DropdownMenuLabel} from '@/base/ui/dropdownMenu'

export const WuMenuItemGroup: React.FC<
  React.ComponentProps<typeof DropdownMenuGroup> & {label: string}
> = props => {
  const {children, ...rest} = props
  return (
    <DropdownMenuGroup {...rest}>
      <DropdownMenuLabel>{props.label}</DropdownMenuLabel>
      {children}
    </DropdownMenuGroup>
  )
}
