import {format} from 'date-fns'
import React from 'react'
import {DateRange} from 'react-day-picker'

import {Label} from '@/base/ui/label'
import {Popover, PopoverContent} from '@/base/ui/popover'
import {cn} from '@/lib/utils'
import {WuCalender} from '../..'
import {WuDatePickerFooter} from '../../ui/_footer'
import {WuDateRangeSidebar} from '../../ui/_sidebar'
import {WuDatePickerTrigger} from '../../ui/_trigger'
import {disabledDate, parsedDate} from '../../utils'

export interface IWuDateRangePickerProps {
  formatString?: string
  value?: DateRange
  disabled?: boolean
  minDate?: Date
  maxDate?: Date
  className?: string
  dir?: 'ltr' | 'rtl'
  onChange?: (date: DateRange | undefined) => void
  minDateToSelect?: number
  maxDateToSelect?: number
  presetRanges?: {label: string; value: number}[]
  label?: string
  labelPosition?: 'top' | 'left'
  onReset?: () => void
  showResetButton?: boolean
  placeholder?: string
}

export const WuDateRangePicker: React.FC<IWuDateRangePickerProps> = ({
  formatString = 'yyyy/MM/dd',
  value,
  onChange,
  disabled,
  minDate,
  maxDate,
  className,
  dir = 'ltr',
  minDateToSelect,
  maxDateToSelect,
  presetRanges,
  label,
  labelPosition = 'top',
  onReset,
  showResetButton = true,
  placeholder = 'Select Date Range',
}) => {
  const [date, setDate] = React.useState<DateRange | undefined>(value)
  const [temporaryDate, setTemporaryDate] = React.useState<
    DateRange | undefined
  >(value)
  const [formatted, setFormatted] = React.useState<string>('')

  // Updates the confirmed date and formatted string when "Apply" is pressed
  const handleApply = React.useCallback(() => {
    if (!temporaryDate || !temporaryDate.from || !temporaryDate.to) return

    const from = temporaryDate.from
    const to = temporaryDate.to

    setDate({from, to})
    setFormatted(`${format(from, formatString)} - ${format(to, formatString)}`)

    if (onChange) {
      onChange({from, to})
    }
  }, [temporaryDate, formatString, onChange])

  // Resets to the last confirmed state when the picker is closed without applying
  const handlePopoverClose = React.useCallback(() => {
    setTemporaryDate(date)
  }, [date])

  // Resets to initial state and calls onChange with undefined
  const handleOnReset = React.useCallback(() => {
    if (onReset) {
      onReset()
    } else {
      setDate(undefined)
      setTemporaryDate(undefined)
      setFormatted('')
      if (onChange) {
        onChange(undefined)
      }
    }
  }, [onReset, onChange])

  React.useEffect(() => {
    if (value?.from && value?.to) {
      const from = parsedDate(value.from)
      const to = parsedDate(value.to)

      setDate(value)
      setTemporaryDate(value)
      setFormatted(
        `${format(from, formatString)} - ${format(to, formatString)}`,
      )
    }
  }, [value, formatString])

  return (
    <Label
      className={cn(
        'flex gap-1',
        labelPosition === 'left' ? 'items-center' : 'flex-col items-start',
        'wu-date-picker-label',
      )}
      dir={dir}
    >
      {label}
      <Popover onOpenChange={isOpen => !isOpen && handlePopoverClose()}>
        <WuDatePickerTrigger
          formattedString={formatted}
          dir={dir}
          placeholder={placeholder}
        />

        <PopoverContent
          className="flex p-0 border w-fit border-blue-p"
          align="start"
          sideOffset={-0.5}
          dir={dir}
        >
          <WuDateRangeSidebar
            current={temporaryDate}
            onSelect={setTemporaryDate}
            presetRanges={presetRanges}
          />
          <WuCalender
            dir={dir}
            numberOfMonths={2}
            mode="range"
            captionLayout="dropdown"
            selected={temporaryDate}
            onSelect={setTemporaryDate}
            disabled={d => disabledDate(d, disabled, minDate, maxDate)}
            startMonth={minDate}
            endMonth={maxDate}
            required
            autoFocus
            defaultMonth={date?.from}
            className={cn('shadow-none', className)}
            min={minDateToSelect}
            max={maxDateToSelect}
            footer={
              <WuDatePickerFooter
                handleDateChange={handleApply}
                disabled={disabled}
                onReset={handleOnReset}
                showResetButton={showResetButton}
              />
            }
          />
        </PopoverContent>
      </Popover>
    </Label>
  )
}
