import {Command, CommandEmpty, CommandList} from '@/base/ui/command'
import {Label} from '@/base/ui/label'
import {Popover, PopoverContent} from '@/base/ui/popover'
import {ScrollArea} from '@/base/ui/scrollArea'
import {cn, getLabelPositionClass} from '@/lib/utils'
import React, {
  memo,
  useCallback,
  useEffect,
  useLayoutEffect,
  useMemo,
  useRef,
  useState,
} from 'react'
import {WuInput} from '../input'
import {WuVirtualScroll} from '../virtualScroll'
import {IWuComboboxItem, IWuComboboxOption} from './types/IWuComboboxOptions'
import {WuComboboxTrigger} from './ui/_trigger'
import {RenderItems, separateOptions} from './utils'
import {useResizeContent} from './utils/useResizeContent'

type IComboboxValue<T> = IWuComboboxItem<T> | IWuComboboxItem<T>[]

export interface IWuComboboxProps<T> {
  data: T[]
  accessorKey: {value: string; label: string}
  defaultValue?: T | T[] | null
  value?: T | T[] | null
  Header?: React.ReactNode
  virtualizedThreshold?: number
  isEllipse?: boolean
  placeholder?: string
  variant?: 'flat' | 'outlined'
  dir?: 'ltr' | 'rtl'
  label?: string
  maxHeight?: number
  onSelect?: (value: T | T[]) => void
  hasGroup?: boolean
  multiple?: boolean
  selectAll?: {
    enable: boolean
    label?: string
    includeDisabled?: boolean
    triggerText?: string
  }
  id?: string
  disabled?: boolean
  labelPosition?: 'left' | 'top' | 'right'
}

// Memoized components
// const MemoizedRenderItems = memo(RenderItems)
const MemoizedLabel = memo(Label)
const MemoizedInput = memo(WuInput)

export const WuCombobox = <T,>({
  data,
  accessorKey,
  value,
  defaultValue,
  Header,
  virtualizedThreshold = 100,
  isEllipse,
  placeholder,
  variant,
  dir,
  onSelect,
  label,
  maxHeight = 420,
  multiple = false,
  hasGroup = false,
  selectAll,
  id,
  disabled,
  labelPosition = 'top',
}: IWuComboboxProps<T>): React.JSX.Element => {
  const [search, setSearch] = useState<string>('')
  const [selectedOption, setSelectedOption] = useState<IComboboxValue<T>>()
  const [isOpen, setIsOpen] = useState(false)
  const [isCheckedAll, setIsCheckedAll] = useState(false)
  const [isPartial, setIsPartial] = useState(false)

  const parentRef = useRef<HTMLDivElement>(null)
  const [labelWidth, setLabelWidth] = useState(0)
  const labelRef = useRef<HTMLDivElement>(null)

  useLayoutEffect(() => {
    if (labelPosition === 'left' && labelRef.current) {
      setLabelWidth(labelRef.current.offsetWidth + 8)
    } else {
      setLabelWidth(0)
    }
  }, [labelPosition])

  const contentWidth = useResizeContent(parentRef, labelWidth)

  const {allOptions, groups, dividers, enabledOptions} = useMemo(
    () =>
      hasGroup || (selectAll && !selectAll.includeDisabled)
        ? separateOptions(data as IWuComboboxOption<T>[])
        : {allOptions: data, groups: [], dividers: [], enabledOptions: []},
    [data, hasGroup],
  )

  const filteredInitialOptions = useMemo(() => {
    if (!search) return data
    const searchLower = search.toLowerCase()
    return allOptions.filter(option =>
      ((option as T)[accessorKey.label as keyof T] as string)
        .toLowerCase()
        .includes(searchLower),
    )
  }, [search, allOptions, data, accessorKey.label])

  const filteredOptions = useMemo(() => {
    if (!multiple) {
      return filteredInitialOptions
    }
    if (selectAll?.enable && !search && filteredInitialOptions.length > 0) {
      return [{type: 'all'}, ...filteredInitialOptions]
    } else return filteredInitialOptions
  }, [filteredInitialOptions, search, multiple])

  const scrollHeight = useMemo(() => {
    const totalHeight =
      32 * filteredOptions.length + dividers.length * 9 + groups.length * 48
    return Math.min(maxHeight, totalHeight)
  }, [maxHeight, filteredOptions, dividers, groups])

  const handleSelect = useCallback(
    (option: IWuComboboxItem<T>) => {
      const optionValue = option[accessorKey.value as keyof T]

      if (multiple) {
        setSelectedOption(prevValue => {
          const currentSelection = (prevValue || []) as IWuComboboxItem<T>[]
          const isSelected = currentSelection.some(
            item => item[accessorKey.value as keyof T] === optionValue,
          )

          const updatedSelection = isSelected
            ? currentSelection.filter(
                item => item[accessorKey.value as keyof T] !== optionValue,
              )
            : [...currentSelection, option]

          onSelect?.(updatedSelection)
          return updatedSelection
        })
      } else {
        setIsOpen(false)
        setSelectedOption(option)
        onSelect?.(option)
      }
    },
    [multiple, accessorKey.value, onSelect],
  )

  const handleSearchChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => setSearch(e.target.value),
    [],
  )

  const resetFilter = useCallback(() => setSearch(''), [])

  useEffect(() => {
    if (value !== undefined && defaultValue !== undefined) {
      throw new Error("Can't use both value and defaultValue together.")
    } else if (value !== undefined) {
      setSelectedOption(value as IComboboxValue<T>)
    } else if (defaultValue !== undefined) {
      setSelectedOption(defaultValue as IComboboxValue<T>)
    }
  }, [value, defaultValue])

  useEffect(() => {
    if (!multiple) return

    const isAllSelected = selectAll?.includeDisabled
      ? (selectedOption as T[])?.length === allOptions.length
      : (selectedOption as T[])?.length === enabledOptions.length
    setIsCheckedAll(isAllSelected)

    const isPartialSelected =
      (selectedOption as T[])?.length > 0 && !isAllSelected
    setIsPartial(isPartialSelected)
  }, [selectedOption, selectAll])

  const handleSelectAll = useCallback((): void => {
    if (!multiple) return

    const newSelectedOptions = isCheckedAll
      ? []
      : selectAll?.includeDisabled
        ? allOptions
        : enabledOptions

    setSelectedOption(newSelectedOptions as IComboboxValue<T>)
    onSelect?.(newSelectedOptions)
  }, [isCheckedAll, multiple, allOptions, enabledOptions, onSelect, selectAll])

  const comboboxContainerClass = getLabelPositionClass(labelPosition)

  return (
    <div
      className={cn(
        comboboxContainerClass,
        (labelPosition === 'right' || labelPosition === 'left') &&
          'items-center',
        'flex wu-combobox-container',
      )}
      ref={parentRef}
    >
      <Popover
        onOpenChange={open => {
          if (open) resetFilter()
          setIsOpen(open)
        }}
        open={isOpen}
        modal
      >
        <WuComboboxTrigger
          selectedOptions={selectedOption}
          accessorKey={accessorKey}
          isEllipse={isEllipse}
          placeholder={placeholder}
          variant={variant}
          dir={dir}
          multiple={multiple}
          disabled={disabled}
          selectAll={selectAll}
          isCheckedAll={isCheckedAll}
        />
        <PopoverContent
          className="p-0 bg-white wu-combobox-content "
          align="start"
          style={{width: `${contentWidth}px`}}
        >
          <Command id={id}>
            <div className="m-2">
              <MemoizedInput
                placeholder="Search..."
                Icon={<span className="wm-search" />}
                className="text-xs wu-combobox-search"
                onChange={handleSearchChange}
                name="Search"
                aria-label="Search"
                dir={dir}
                iconPosition={dir === 'rtl' ? 'right' : 'left'}
              />
            </div>
            {Header}
            <CommandList>
              <CommandEmpty>No results found.</CommandEmpty>
              {filteredOptions.length >= virtualizedThreshold ? (
                <WuVirtualScroll
                  className="wu-combobox-list"
                  containerHeight={maxHeight}
                  list={filteredOptions}
                  callback={(i, option) => (
                    <RenderItems
                      key={i}
                      option={option as IWuComboboxItem<T>}
                      value={selectedOption}
                      onSelect={handleSelect}
                      multiple={multiple}
                      accessorKey={accessorKey}
                      dir={dir}
                      selectAll={selectAll}
                      handleSelectAll={handleSelectAll}
                      isCheckedAll={isCheckedAll}
                      isPartialSelected={isPartial}
                    />
                  )}
                  dir={dir}
                />
              ) : (
                <ScrollArea
                  style={{maxHeight, height: scrollHeight}}
                  className="wu-combobox-list"
                >
                  {filteredOptions.map((option, i) => (
                    <RenderItems
                      key={i}
                      option={option as IWuComboboxItem<T>}
                      value={selectedOption}
                      onSelect={handleSelect}
                      multiple={multiple}
                      accessorKey={accessorKey}
                      dir={dir}
                      selectAll={selectAll}
                      handleSelectAll={handleSelectAll}
                      isCheckedAll={isCheckedAll}
                      isPartialSelected={isPartial}
                    />
                  ))}
                </ScrollArea>
              )}
            </CommandList>
          </Command>
        </PopoverContent>
      </Popover>
      {label && (
        <div ref={labelRef} dir={dir}>
          <MemoizedLabel
            className="text-xs font-normal leading-6 text-gray-lead truncate max-w-[120px] min-w-fit wu-combobox-label"
            dir={dir}
            htmlFor={id}
            aria-disabled={disabled}
          >
            {label}
          </MemoizedLabel>
        </div>
      )}
    </div>
  )
}
