import {Avatar, AvatarFallback, AvatarImage} from '@/base/ui/avatar'
import {WuButton} from '@/components/button'
import {cn} from '@/lib/utils'
import React, {useRef, useState} from 'react'
import {useOnClickOutside} from '../hooks/useOnClickOutsideHook'
import {IWuAppHeaderAccount} from '../types/WuAppHeaderAccount'
import './WuAppHeaderHelpAndAccount.css'

interface IWuAppHeaderAccountProps {
  user?: IWuAppHeaderAccount
  onLogout?: () => void
  dir?: 'ltr' | 'rtl'
  DataCenter?: React.ReactElement
}

export const WuAppHeaderAccount: React.FC<IWuAppHeaderAccountProps> = ({
  user,
  onLogout,
  dir,
  DataCenter,
}) => {
  const [isOpen, setIsOpen] = useState(false)
  const dropdownRef = useRef<HTMLDivElement | null>(null)
  const buttonRef = useRef<HTMLButtonElement>(null)

  useOnClickOutside([dropdownRef, buttonRef], () => setIsOpen(false))

  const toggleDropdown = (): void => {
    setIsOpen(!isOpen)
  }

  if (!user) return null

  return (
    <>
      <div className="dropdown-container">
        <ul>
          <li className={cn('flex relative', isOpen && 'open')}>
            <button
              className="rounded-full focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-soft focus-visible:ring-offset-2 focus-visible:ring-offset-gray-switcher w-8 h-8 wu-app-user-button"
              onClick={e => {
                e.preventDefault()
                toggleDropdown()
              }}
              ref={buttonRef}
            >
              <Avatar
                className={cn(
                  'group border border-white text-sm font-medium bg-transparent w-8 h-8 ring-white leading-[33px] text-white transition-colors hover:bg-white hover:text-gray-switcher wu-header-user-avatar',
                  isOpen && 'bg-white text-gray-switcher',
                )}
              >
                <AvatarImage
                  className="hover:opacity-80"
                  src={user?.profile?.profilePicture}
                  alt="User"
                />
                <AvatarFallback>{user?.profile?.initials}</AvatarFallback>
              </Avatar>
            </button>
            <div
              className={cn(
                'dropdown-menu',
                {open: isOpen},
                dir === 'rtl'
                  ? '-left-[10px] top-[38px]'
                  : '-right-[7px] top-[38px]',
              )}
              dir={dir}
              ref={dropdownRef}
            >
              <div
                className={cn(
                  'p-0 text-xs font-medium border rounded-none min-w-72 w-max max-w-96 border-gray-subtle text-gray-lead overflow-hidden bg-white',
                )}
                dir={dir}
                ref={dropdownRef}
              >
                <UserMenuItem user={user} dir={dir} />
                {user?.settings && user.settings.length > 0 && (
                  <SettingsMenuItem settings={user.settings} dir={dir} />
                )}
                <OtherMenuItem user={user} dir={dir} />
                <LogoutMenuItem onLogout={onLogout} DataCenter={DataCenter} />
              </div>
            </div>
          </li>
        </ul>
      </div>
    </>
  )
}

const UserMenuItem: React.FC<{
  user: IWuAppHeaderAccount
  dir?: 'ltr' | 'rtl'
}> = ({user, dir}) => (
  <section
    className={cn('flex gap-2 p-4 w-full', dir === 'rtl' ? 'pr-0' : 'pl-0')}
  >
    <div className="flex items-center justify-center w-12">
      <span className="text-base wm-person"></span>
    </div>
    <div className={cn('flex flex-col gap-1', dir === 'rtl' && 'text-right')}>
      <p>{user?.profile?.title}</p>
      <a
        href={user?.license?.url}
        className="hover:text-blue-p group/license focus:outline-none focus:text-blue-p"
        dir={dir}
      >
        <p>{user?.profile?.subtitle}</p>
        <p className="font-light">{user?.profile?.companyName}</p>
      </a>
      {user?.license && (
        <div className="flex items-center justify-between gap-6 mt-4" dir={dir}>
          <a
            href={user?.license?.url}
            className="hover:text-blue-p group/license focus:outline-none focus:text-blue-p"
          >
            <div className="flex flex-col items-start gap-1">
              <p className="font-normal">{user?.license?.subtitle}</p>
              <button
                tabIndex={-1}
                className="px-3 py-1 border rounded-sm border-gray-subtle group-hover/license:border-blue-p"
              >
                {user?.license?.title}
              </button>
            </div>
          </a>
          <div className="flex flex-col items-start gap-1" dir={dir}>
            <p className="font-normal">
              {user?.license?.expiryDatePostFixString}
            </p>
            <p>{user?.license?.expiryDate}</p>
          </div>
        </div>
      )}
    </div>
  </section>
)

const LogoutMenuItem: React.FC<{
  onLogout?: () => void
  DataCenter?: React.ReactElement
}> = ({onLogout, DataCenter}) => (
  <section className="flex justify-between px-4 py-4 text-white bg-gray-lead">
    <div className="flex-1 flex items-start">{DataCenter}</div>
    <WuButton
      variant="outline"
      size="sm"
      Icon={<span className="wm-logout"></span>}
      className="text-white bg-transparent ring-white hover:bg-gray-10 hover:text-gray-lead hover:ring-gray-lead"
      onClick={onLogout}
      aria-label="logout"
    >
      LOGOUT
    </WuButton>
  </section>
)

const SettingsMenuItem: React.FC<{
  settings: IWuAppHeaderAccount['settings']
  dir?: 'ltr' | 'rtl'
}> = ({settings, dir}) => (
  <>
    <hr className="border-gray-subtle" />
    <section className="relative flex items-center justify-start gap-2">
      <span
        className={cn(
          'absolute wm-settings text-base',
          dir === 'rtl' ? 'right-4' : 'left-4',
        )}
      ></span>
      <ul className="w-full" dir={dir}>
        {settings?.map(item => (
          <li key={item.title} tabIndex={-1} dir={dir}>
            <a
              href={item.url}
              className={cn(
                'inline-block w-full py-2 hover:bg-gray-20 focus-within:bg-gray-20 focus:outline-none',
                dir === 'rtl' ? 'pr-14 text-right' : 'pl-14',
              )}
            >
              {item.title}
            </a>
          </li>
        ))}
      </ul>
    </section>
    <hr className="border-gray-subtle" />
  </>
)

const OtherMenuItem: React.FC<{
  user: IWuAppHeaderAccount
  dir?: 'ltr' | 'rtl'
}> = ({user, dir}) => {
  const otherList = [
    user?.usage
      ? {
          title: user.usage.title,
          url: user.usage.url,
          icon: 'wm-bar-chart text-base',
          node: (
            <div className="flex flex-col items-center w-20">
              <code className="font-medium">
                {user.usage.collectedResponseCount ?? 0}
              </code>
              <span className="text-xs font-normal">Responses</span>
            </div>
          ),
        }
      : null,
    user?.invoice
      ? {
          title: user.invoice.title,
          url: user.invoice.url,
          icon: 'wm-attach-money text-base font-semibold',
          node: (
            <div className="flex flex-col items-center w-20">
              <code>{user.invoice.size}</code>
            </div>
          ),
        }
      : null,
  ].filter(Boolean) as {
    title: string
    url: string
    icon: string
    node: React.JSX.Element
  }[]

  return (
    <>
      <ul>
        {otherList.map(({title, url, icon, node}, i) => (
          <li
            key={i}
            className={
              i < otherList.length - 1 ? 'border-b border-gray-subtle' : ''
            }
          >
            <a
              href={url}
              className={cn(
                'flex items-center justify-between py-2 hover:bg-gray-20 focus-within:bg-gray-20 focus:outline-none',
                dir === 'rtl' ? 'pl-4' : 'pr-4',
              )}
            >
              <div className="flex items-center gap-2">
                <span className={cn(icon, 'w-12 text-center')}></span>
                <span>{title}</span>
              </div>
              {node}
            </a>
          </li>
        ))}
      </ul>
      <hr className="border-gray-subtle" />
    </>
  )
}
