import {cn} from '@/lib/utils'
import React, {useEffect, useState} from 'react'

interface IWuInputHintProps extends React.HTMLAttributes<HTMLParagraphElement> {
  inputRef?: React.RefObject<HTMLInputElement | HTMLTextAreaElement | null>
  maxLength?: number
}

export const WuInputHint: React.FC<IWuInputHintProps> = props => {
  const {inputRef, maxLength = 50, className, ...rest} = props
  const [showHint, setShowHint] = useState(false)

  useEffect(() => {
    if ((inputRef?.current?.value.length || 0) > 0.75 * maxLength) {
      setShowHint(true)
    } else {
      setShowHint(false)
    }
  }, [inputRef?.current?.value, maxLength])

  return (
    <p {...rest} className={cn('wu-input-hint', className)}>
      {showHint && `${inputRef?.current?.value.length}/${maxLength}`}
    </p>
  )
}
