export const disabledDate = (
  e: Date,
  disabled?: boolean,
  minDate?: Date,
  maxDate?: Date,
): boolean => {
  if (disabled) return true
  if (minDate && e < minDate) return true
  if (maxDate && e > maxDate) return true
  return false
}

export const parsedDate = (value?: Date | string): Date =>
  value ? (typeof value === 'string' ? new Date(value) : value) : new Date()

export const INPUT_PATTERN = '\\d{4}-(0[1-9]|1[0-2])-(0[1-9]|[12]\\d|3[01])'
