import {Popover, PopoverContent, PopoverTrigger} from '@/base/ui/popover'
import {cn} from '@/lib/utils'
import {PopoverClose, PopoverContentProps} from '@radix-ui/react-popover'
import React from 'react'
import {IWuButtonProps, WuButton} from '../button'

export type IWuPopoverProps = PopoverContentProps & {
  Trigger: React.ReactNode
  children: React.ReactNode
  className?: string
  dir?: 'ltr' | 'rtl'
  open?: boolean
  onOpenChange?: (open: boolean) => void
  modal?: boolean
}

export const WuPopover: React.FC<IWuPopoverProps> = ({
  Trigger,
  children,
  className,
  dir,
  open,
  onOpenChange,
  modal,
  ...rest
}) => {
  return (
    <Popover open={open} onOpenChange={onOpenChange} modal={modal}>
      <PopoverTrigger asChild dir={dir}>
        {Trigger}
      </PopoverTrigger>

      <PopoverContent
        className={cn(
          'w-80 p-0 border-blue-p bg-white dark:border-gray-switcher',
          className,
        )}
        dir={dir}
        {...rest}
      >
        {children}
      </PopoverContent>
    </Popover>
  )
}

export const WuPopoverClose: React.FC<IWuButtonProps> = props => {
  return (
    <PopoverClose asChild>
      <WuButton {...props}></WuButton>
    </PopoverClose>
  )
}
