import {CommandGroup, CommandItem, CommandSeparator} from '@/base/ui/command'
import {DropdownMenuLabel} from '@/base/ui/dropdownMenu'
import {WuCheckbox} from '@/components/checkbox'
import {cn} from '@/lib/utils'
import React from 'react'
import {
  IWuComboboxGroup,
  IWuComboboxItem,
  IWuComboboxOption,
} from '../types/IWuComboboxOptions'
import {IWuComboboxItemProps, WuComboboxItem} from '../ui/_item'
import {IWuComboboxProps} from '../WuCombobox'

type IRenderComboboxItemsProps<T> = Omit<IWuComboboxItemProps<T>, 'option'> & {
  option: IWuComboboxOption<T>
  selectAll?: IWuComboboxProps<T>['selectAll']
  handleSelectAll?: () => void
  isCheckedAll?: boolean
  isPartialSelected?: boolean
}

export const RenderItems = <T,>(
  props: IRenderComboboxItemsProps<T>,
): React.JSX.Element => {
  const {
    option,
    selectAll,
    handleSelectAll,
    isCheckedAll,
    isPartialSelected,
    ...rest
  } = props
  const type = 'type' in option ? (option.type as string) : 'option'

  switch (type) {
    case 'all':
      return (
        <CommandItem
          onSelect={handleSelectAll}
          className={cn('cursor-pointer text-xs h-8 py-0 wu-select-item')}
          dir={rest.dir}
        >
          <WuCheckbox
            checked={isCheckedAll || isPartialSelected}
            partial={isPartialSelected}
          />
          {selectAll?.label || 'Select All'}
        </CommandItem>
      )
    case 'group':
      return (
        <CommandGroup>
          <DropdownMenuLabel>
            {(option as IWuComboboxGroup<T>).label}
          </DropdownMenuLabel>
          {(option as IWuComboboxGroup<T>).options.map((opt, i) => (
            <WuComboboxItem
              {...rest}
              option={opt as IWuComboboxItem<T>}
              key={i}
            />
          ))}
        </CommandGroup>
      )
    case 'divider':
      return <CommandSeparator className="mx-4" />

    default:
      return <WuComboboxItem {...rest} option={option as IWuComboboxItem<T>} />
  }
}
