import {WuIcon} from '@/components/typography'
import {
  Toast,
  ToastClose,
  ToastDescription,
  ToastProvider,
  ToastTitle,
  ToastViewport,
} from './toast'
import {useToast} from './useToast'
import {ICON_CLASSES} from '@/components/typography/iconClasses'

export function Toaster(): React.JSX.Element {
  const {toasts} = useToast()

  return (
    <ToastProvider>
      {toasts.map(function ({
        id,
        title,
        description,
        action,
        variant,
        ...props
      }) {
        const variantToIcon: {[key: string]: (typeof ICON_CLASSES)[number]} = {
          success: 'wm-check-circle',
          info: 'wm-info',
          warning: 'wm-error',
          error: 'wm-cancel',
        }
        return (
          <Toast key={id} variant={variant} {...props}>
            <div className="grid gap-1">
              {title && <ToastTitle>{title}</ToastTitle>}
              {description && (
                <ToastDescription className="flex items-center gap-3">
                  <WuIcon
                    icon={variantToIcon[variant || 'success']}
                    className="text-xl"
                  />
                  {description}
                </ToastDescription>
              )}
            </div>
            {action}
            <ToastClose />
          </Toast>
        )
      })}
      <ToastViewport />
    </ToastProvider>
  )
}
