import {
  DropdownMenuRadioGroup,
  DropdownMenuRadioItem,
} from '@/base/ui/dropdownMenu'

export interface IWuMenuRadioItemOption {
  value: string
  label: string
  disabled?: boolean
}
export interface IWuMenuRadioItemProps {
  options: IWuMenuRadioItemOption[]
  value: string
  onSelect: (value: string) => void
}
export const WuMenuRadioItem: React.FC<IWuMenuRadioItemProps> = props => {
  const {options, value, onSelect} = props
  return (
    <DropdownMenuRadioGroup value={value} onValueChange={onSelect}>
      {options.map(option => (
        <DropdownMenuRadioItem
          key={option.value}
          value={option.value}
          disabled={option.disabled}
        >
          {option.label}
        </DropdownMenuRadioItem>
      ))}
    </DropdownMenuRadioGroup>
  )
}
