import {PopoverTrigger} from '@/base/ui/popover'
import {cn} from '@/lib/utils'
import React, {useEffect, useRef, useState} from 'react'
import {IWuComboboxItem} from '../types/IWuComboboxOptions'
import {IWuComboboxProps} from '../WuCombobox'
import {WuRenderTags} from './_tags'

export interface IWuComboboxTriggerProps<T> {
  accessorKey: {label: string; value: string}
  placeholder?: string
  isEllipse?: boolean
  selectedOptions?: IWuComboboxItem<T> | IWuComboboxItem<T>[]
  variant?: 'flat' | 'outlined'
  dir?: 'ltr' | 'rtl'
  multiple?: boolean
  id?: string
  disabled?: boolean
  selectAll?: IWuComboboxProps<T>['selectAll']
  isCheckedAll?: boolean
}

export const WuComboboxTrigger = <T,>({
  placeholder = 'Select...',
  isEllipse = false,
  selectedOptions,
  variant = 'flat',
  dir = 'ltr',
  accessorKey,
  multiple,
  id,
  disabled,
  selectAll,
  isCheckedAll,
}: IWuComboboxTriggerProps<T>): React.JSX.Element => {
  const [tags, setTags] = useState<string[]>([])
  const triggerRef = useRef<HTMLButtonElement>(null)

  useEffect(() => {
    if (
      !selectedOptions ||
      (Array.isArray(selectedOptions) && selectedOptions.length === 0)
    ) {
      setTags([])
      return
    }
    if (multiple) {
      const temp = selectedOptions as IWuComboboxItem<T>[]

      setTags(
        temp?.map(option => option[accessorKey.label as keyof T] as string) ||
          [],
      )
      return
    } else {
      const temp = selectedOptions as IWuComboboxItem<T>
      setTags([temp[accessorKey.label as keyof T] as string])
    }
  }, [selectedOptions])

  const renderContent = (): React.JSX.Element => {
    if (!tags.length) return <p className="text-xs opacity-70">{placeholder}</p>
    if (isCheckedAll && selectAll?.enable && selectAll?.triggerText) {
      return (
        <span className="overflow-hidden text-xs text-start text-ellipsis whitespace-nowrap">
          {selectAll.triggerText}
        </span>
      )
    }

    if (isEllipse) {
      return (
        <span
          className="overflow-hidden text-xs text-start text-ellipsis whitespace-nowrap"
          style={{width: `${(triggerRef.current?.offsetWidth || 0) - 32}px`}}
        >
          {tags.join(', ')}
        </span>
      )
    }

    return <WuRenderTags tags={tags} triggerRef={triggerRef} dir={dir} />
  }

  return (
    <PopoverTrigger dir={dir} asChild id={id} disabled={disabled}>
      <button
        ref={triggerRef}
        disabled={disabled}
        className={cn(
          'select-trigger text-xs rounded-t-sm flex gap-2 h-8 group wu-combobox-trigger',
          variant === 'outlined' && 'border border-gray-subtle',
          'disabled:cursor-not-allowed disabled:hover:border-gray-subtle disabled:bg-gray-10',
        )}
      >
        {renderContent()}
        <span className="wm-arrow-drop-down group-data-[state=open]:rotate-180 transition-transform duration-300 text-base"></span>
      </button>
    </PopoverTrigger>
  )
}
