import {cn} from '@/lib/utils'
import React from 'react'

export interface IWuFormGroupProps
  extends React.HTMLAttributes<HTMLDivElement> {
  Label?: React.ReactNode
  Error?: React.ReactNode
  Hint?: React.ReactNode
  Input: React.ReactNode
  labelPosition?: 'left' | 'top'
  dir?: 'ltr' | 'rtl'
}

export const WuFormGroup: React.FC<IWuFormGroupProps> = ({
  Label,
  Error,
  Hint,
  Input,
  labelPosition = 'top',
  dir = 'ltr',
  className,
  ...props
}: IWuFormGroupProps) => {
  const isTopLabel = labelPosition === 'top'

  const containerClasses = cn(
    'flex',
    isTopLabel ? 'flex-col gap-1' : 'flex-row items-center gap-2',
    className,
  )

  const wrapperClasses = cn(
    'flex flex-col gap-1 relative flex-1 wu-form-group-wrapper',
  )

  const hintPositionClasses = cn(
    'absolute -bottom-5',
    dir === 'ltr' ? 'right-0' : 'left-0',
  )

  const errorPositionClasses = cn(
    'absolute -bottom-5',
    dir === 'ltr' ? 'left-0' : 'right-0',
  )

  return (
    <div className={containerClasses} dir={dir} {...props}>
      {Label}
      <div className={wrapperClasses}>
        {Input}
        {Hint && <div className={hintPositionClasses}>{Hint}</div>}
        {Error && <div className={errorPositionClasses}>{Error}</div>}
      </div>
    </div>
  )
}
