import {IWuSelectItem, IWuSelectOption} from '../types/IWuSelectOptions'

export const useOptionDestructure = <T>(
  options: IWuSelectOption<T>[],
): [number, number, number, IWuSelectItem<T>[]] => {
  let noOfSeparators = 0
  let noOfGroups = 0
  const temp: IWuSelectItem<T>[] = []
  const enabledOptions: IWuSelectItem<T>[] = []

  for (let i = 0; i < options.length; i++) {
    const option = options[i]
    if (!(option as IWuSelectItem<T>)?.disabled) {
      enabledOptions.push(option as IWuSelectItem<T>)
    }

    if (option.type === 'divider') {
      noOfSeparators++
    } else if (option.type === 'group') {
      noOfGroups++
      temp.push(...(option.options as IWuSelectItem<T>[]))
      enabledOptions.push(
        ...(option.options.filter(
          option => !option.disabled,
        ) as IWuSelectItem<T>[]),
      )
    } else {
      temp.push(option as IWuSelectItem<T>)
    }
  }
  return [temp.length, noOfSeparators, noOfGroups, enabledOptions]
}
