import React from 'react'

/**
 *
 * @param list the list of items to manipulate
 * @param parentRef the parent div ref, used to calculate the width of the parent div
 * @param childRef the child div ref, this is the div(s) will not be resized i.e. other elements in the parent
 * @param minWidthPerItem minimum width of the item nodes
 * @returns an array of open items and an array of wrapped items
 */
export const useResizeList = (
  list: React.ReactElement[],
  parentRef: React.RefObject<HTMLDivElement | null>,
  childRef: React.RefObject<HTMLDivElement | null>,
  minWidthPerItem = 96,
): [React.ReactElement[], React.ReactElement[]] => {
  const nullLogic = list.length === 0 || minWidthPerItem <= 0

  if (nullLogic) return [[], []]
  const [openItems, setOpenItems] = React.useState<React.ReactElement[]>([])
  const [wrappedItems, setWrappedItems] = React.useState<React.ReactElement[]>(
    [],
  )
  React.useEffect(() => {
    const handleResize = (): void => {
      const pw = parentRef.current?.clientWidth || 0
      const cw = childRef.current?.clientWidth || 0

      const targetWidth = pw - cw
      const maxItem = Math.floor(targetWidth / minWidthPerItem)

      setOpenItems(list.slice(0, maxItem))
      setWrappedItems(list.slice(maxItem))
    }
    handleResize()
    window.addEventListener('resize', handleResize)
    return (): void => {
      window.removeEventListener('resize', handleResize)
    }
  }, [list, minWidthPerItem])

  return [openItems, wrappedItems]
}
