import {
  IWuComboboxDivider,
  IWuComboboxGroup,
  IWuComboboxItem,
  IWuComboboxOption,
} from '../types/IWuComboboxOptions'

export const separateOptions = <T>(
  options: IWuComboboxOption<T>[],
): {
  groups: IWuComboboxGroup<T>[]
  dividers: IWuComboboxDivider[]
  allOptions: IWuComboboxItem<T>[]
  enabledOptions: IWuComboboxItem<T>[]
} => {
  const groups: IWuComboboxGroup<T>[] = []
  const dividers: IWuComboboxDivider[] = []
  const allOptions: IWuComboboxItem<T>[] = []
  const enabledOptions: IWuComboboxItem<T>[] = []

  options.forEach(option => {
    if (option.type === 'group') {
      groups.push(option as IWuComboboxGroup<T>)
      allOptions.push(...(option.options as IWuComboboxItem<T>[]))
      enabledOptions.push(
        ...(option.options.filter(
          option => !option.disabled,
        ) as IWuComboboxItem<T>[]),
      )
    } else if (option.type === 'divider') {
      dividers.push(option as IWuComboboxDivider)
    } else {
      allOptions.push(option as IWuComboboxItem<T>)
    }
    if (!(option as IWuComboboxItem<T>)?.disabled) {
      enabledOptions.push(option as IWuComboboxItem<T>)
    }
  })

  return {groups, dividers, allOptions, enabledOptions}
}
