import {
  DropdownMenuPortal,
  DropdownMenuSub,
  DropdownMenuSubContent,
  DropdownMenuSubTrigger,
} from '@/base/ui/dropdownMenu'
import {ScrollArea} from '@/base/ui/scrollArea'

export interface IWuSubMenuProps
  extends Omit<React.ComponentProps<typeof DropdownMenuSub>, 'onOpenChange'> {
  label: string
  open?: boolean
  onOpenChange?: (open: boolean) => void
  height?: string
  dir?: 'rtl' | 'ltr'
}
export const WuSubMenu: React.FC<IWuSubMenuProps> = props => {
  const {children, label, open, onOpenChange, height, dir, ...rest} = props
  return (
    <DropdownMenuSub {...rest} open={open} onOpenChange={onOpenChange}>
      <DropdownMenuSubTrigger dir={dir}>{label}</DropdownMenuSubTrigger>
      <DropdownMenuPortal>
        <DropdownMenuSubContent onInteractOutside={e => e.preventDefault()}>
          <ScrollArea className="h-fit" style={{height}} dir={dir}>
            {children}
          </ScrollArea>
        </DropdownMenuSubContent>
      </DropdownMenuPortal>
    </DropdownMenuSub>
  )
}
