import {TableHead, TableHeader, TableRow} from '@/base/ui/table'
import {cn} from '@/lib/utils'
import {Column, flexRender, Table} from '@tanstack/react-table'
import React from 'react'
import {IWuManualSorting} from '../types/IWuManualSorting'

export const WuTableHeader = <T,>({
  table,
  sortableColumns,
  manual = false,
  onSort,
  size,
  isRowSelected,
  HeaderAction,
}: {
  table: Table<T>
  sortableColumns: string[]
  manual?: boolean
  onSort?: (meta: IWuManualSorting) => void
  size?: 'compact' | 'default'
  isRowSelected?: boolean
  HeaderAction?: React.ReactElement
}): React.JSX.Element => {
  const updateSortIcon = (column: Column<T>): React.ReactNode => {
    const {id, columnDef, getIsSorted, toggleSorting} = column
    const {header} = columnDef

    if (!sortableColumns.includes(id)) {
      return header as React.ReactNode
    }

    const isSorted = getIsSorted()
    const isAsc = isSorted === 'asc'
    const isDesc = isSorted === 'desc'

    const handleSort = (): void => {
      if (manual) {
        toggleSorting(!isDesc)
        onSort?.({id, desc: isDesc})
      } else {
        toggleSorting(column.getIsSorted() === 'asc')
      }
    }

    return (
      <button
        onClick={handleSort}
        aria-label={`Sort by ${header}`}
        className="flex items-center gap-4"
      >
        {header as string}
        <div className="relative">
          <span
            className={cn(
              'absolute -top-[15px]  wm-arrow-drop-down text-xl',
              isAsc && 'hidden',
              !isSorted && '-top-[8px]',
            )}
          ></span>
          <span
            className={cn(
              'absolute -bottom-[13px] wm-arrow-drop-up text-xl',
              isDesc && 'hidden',
              !isSorted && '-bottom-[8px]',
            )}
          ></span>
        </div>
      </button>
    )
  }

  return (
    <TableHeader className="border-t border-black/[12%] bg-gray-20 relative">
      {isRowSelected && HeaderAction && (
        <TableRow
          className={cn(
            'absolute left-10 top-0 flex items-center px-4',
            size === 'compact' ? 'h-8' : 'h-10',
            'bg-gray-20 text-gray-lead text-xs font-medium z-10',
            'w-[calc(100%-40px)]',
            'transition-all duration-300 ease-in-out',
          )}
        >
          <TableHead>{HeaderAction}</TableHead>
        </TableRow>
      )}
      {table.getHeaderGroups().map(headerGroup => (
        <TableRow key={headerGroup.id} className="relative">
          {headerGroup.headers.map((header, index) => {
            const isActionsColumn = header.column.id === 'actions'

            return isActionsColumn ? (
              <TableHead
                key={header.id}
                colSpan={header.colSpan}
                className={cn(
                  'text-gray-lead text-xs font-medium',
                  size === 'compact' ? 'h-8' : 'h-[40px]',
                  isRowSelected && 'opacity-0',
                )}
                style={{width: `${header.getSize()}px`}}
              />
            ) : (
              <TableHead
                key={header.id}
                colSpan={header.colSpan}
                className={cn(
                  'text-gray-lead text-xs font-medium bg-[#EEEEEE]',
                  size === 'compact' ? 'h-8' : 'h-[40px]',
                  index !== 0 && isRowSelected && HeaderAction && 'opacity-30',
                  'relative z-0',
                )}
                style={{width: `${header.getSize()}px`}}
              >
                {header.isPlaceholder
                  ? null
                  : flexRender(
                      updateSortIcon(header.column),
                      header.getContext(),
                    )}
              </TableHead>
            )
          })}
        </TableRow>
      ))}
    </TableHeader>
  )
}
