import {Button} from '@/base/ui/button'
import {WuInput} from '@/components/input'
import {cn} from '@/lib/utils'
import {
  differenceInCalendarDays,
  format,
  isBefore,
  isSameDay,
  isToday,
  isValid,
  sub,
} from 'date-fns'
import React, {useCallback, useEffect, useMemo, useState} from 'react'
import {DateRange} from 'react-day-picker'
import {DEFAULT_RANGES, INPUT_PATTERN} from '../utils'

interface IWuDateRangePickerSidebarProps {
  current?: DateRange
  onSelect: (date: DateRange) => void
  presetRanges?: {label: string; value: number}[]
}

const DATE_FORMAT = 'yyyy-MM-dd'
const TOP_BUTTON_RANGES = [
  {label: 'Day', value: 1},
  {label: 'Week', value: 7},
  {label: 'Month', value: 30},
  {label: 'Quarter', value: 90},
  {label: 'Year', value: 365},
] as const

export const WuDateRangeSidebar: React.FC<IWuDateRangePickerSidebarProps> = ({
  current,
  onSelect,
  presetRanges = DEFAULT_RANGES,
}) => {
  const [range, setRange] = useState<number>()
  const [isCustom, setIsCustom] = useState(false)
  const [startDate, setStartDate] = useState('')
  const [endDate, setEndDate] = useState('')
  const [isRangeReversed, setIsRangeReversed] = useState(false)

  const presetValues = useMemo(
    () => presetRanges.map(range => range.value),
    [presetRanges],
  )

  const btnClass = cn(
    'flex w-full gap-2 px-4 py-2 text-sm text-gray-lead transition-colors',
    'rounded-none hover:bg-gray-10 focus:outline-none focus-visible:ring-2',
    'focus-visible:ring-blue-soft focus-visible:ring-offset-1 text-start',
  )

  const handlePresetSelection = useCallback(
    (days?: number): void => {
      if (days === undefined) {
        setRange(undefined)
        setIsCustom(true)
        return
      }

      const d = Number(days) - 1
      setRange(d)
      setIsCustom(false)
      onSelect({from: sub(new Date(), {days: d}), to: new Date()})
    },
    [onSelect],
  )

  const validateDate = useCallback(
    (dateStr: string, type: 'start' | 'end'): boolean => {
      if (!dateStr || !current?.to || !current?.from) return false

      const regex = new RegExp(INPUT_PATTERN)
      if (!regex.test(dateStr)) return false

      const fromDate = new Date(type === 'start' ? dateStr : current.from)
      const toDate = new Date(type === 'end' ? dateStr : current.to)

      if (isBefore(toDate, fromDate)) {
        setIsRangeReversed(true)
        return false
      }

      setIsRangeReversed(false)
      return !(
        isSameDay(fromDate, current.from) && isSameDay(toDate, current.to)
      )
    },
    [current],
  )

  const handleDateChange = useCallback(
    (value: string, type: 'start' | 'end'): void => {
      const setDate = type === 'start' ? setStartDate : setEndDate
      setDate(value)

      if (validateDate(value, type)) {
        const newRange = {
          from: type === 'start' ? new Date(value) : current?.from,
          to: type === 'end' ? new Date(value) : current?.to,
        }
        onSelect(newRange)
      }
    },
    [current, onSelect, validateDate],
  )

  useEffect(() => {
    if (
      current?.to &&
      current?.from &&
      isValid(current.to) &&
      isValid(current.from)
    ) {
      setStartDate(format(current.from, DATE_FORMAT))
      setEndDate(format(current.to, DATE_FORMAT))

      const diff = differenceInCalendarDays(current.to, current.from)
      if (isToday(current.to) && presetValues.includes(diff + 1)) {
        setRange(diff)
        setIsCustom(false)
      } else {
        setRange(undefined)
        setIsCustom(true)
      }
    }
  }, [current, presetValues])

  return (
    <div className="flex flex-col gap-1 p-2 bg-white w-60 wu-date-picker-sidebar">
      <ul className="flex gap-1 py-2 wu-date-picker-batch-list">
        {TOP_BUTTON_RANGES.map(({label, value}) => (
          <Button
            variant="secondary"
            className="px-1 py-1 text-xs h-fit min-w-fit disabled:bg-white"
            disabled={value !== 1}
            key={value}
          >
            {label}
          </Button>
        ))}
      </ul>

      <ul className="flex flex-col gap-1 wu-date-picker-ranges">
        {presetRanges.map(({label, value}) => (
          <li key={value}>
            <button
              className={cn(btnClass, range === value - 1 && 'bg-blue-p/10')}
              onClick={() => handlePresetSelection(value)}
            >
              {label}
            </button>
          </li>
        ))}
        <li>
          <button
            className={cn(btnClass, isCustom && 'bg-blue-p/10')}
            onClick={() => handlePresetSelection()}
          >
            Custom Range
          </button>
        </li>
      </ul>

      <div className="relative flex gap-3 pt-2 wu-date-picker-range-footer">
        <WuInput
          placeholder="Start Date"
          pattern={INPUT_PATTERN}
          value={startDate}
          onChange={e => handleDateChange(e.target.value, 'start')}
          aria-label="Start Date"
        />
        <WuInput
          placeholder="End Date"
          pattern={INPUT_PATTERN}
          value={endDate}
          onChange={e => handleDateChange(e.target.value, 'end')}
          aria-label="End Date"
        />
        {isRangeReversed ? (
          <small className="absolute text-[10px] leading-[14px] text-red-deep left-0 -bottom-4 text-start">
            End date must be after start date
          </small>
        ) : (
          <small className="absolute right-0 leading-[14px] -bottom-4 text-gray-subtle text-end">
            yyyy-mm-dd
          </small>
        )}
      </div>
    </div>
  )
}
