import {Popover, PopoverContent, PopoverTrigger} from '@/base/ui/popover'
import {useResizeList} from '@/hooks/useResizeList'
import {cn} from '@/lib/utils'
import React from 'react'
import './WuPrimaryNavbar.css'

interface INavLinkProps extends React.AnchorHTMLAttributes<HTMLAnchorElement> {
  active?: boolean
  className?: string
}
export interface IWuPrimaryNavbarProps
  extends React.HTMLAttributes<HTMLDivElement> {
  linkNodes?: React.ReactElement<INavLinkProps>[]
  Links: React.ReactElement<INavLinkProps>[]
  children?: React.ReactNode
  className?: string
}

export const WuPrimaryNavbar: React.FC<IWuPrimaryNavbarProps> = props => {
  const {Links, children, className, ...rest} = props

  const modifiedNodes = React.useMemo(
    () =>
      Links.map((n, i) => {
        return React.cloneElement(n, {
          className: cn(
            'primary-link',
            n.props.className,
            'wu-primary-nav-link',
          ),
          key: i,
        })
      }),
    [Links],
  )

  const headerRef = React.useRef<HTMLDivElement>(null)
  const childrenRef = React.useRef<HTMLDivElement>(null)
  const [openItems, wrappedItems] = useResizeList(
    modifiedNodes,
    headerRef,
    childrenRef,
  )
  return (
    <>
      <header
        className={cn(
          'flex items-center justify-between gap-4 px-2 py-1 bg-white border-b',
          className,
        )}
        {...rest}
        ref={headerRef}
      >
        <div className="flex items-center gap-2">
          <nav className="flex items-center justify-start gap-4 wu-primary-nav-left">
            {openItems}
          </nav>
          {wrappedItems.length > 0 && (
            <Popover>
              <PopoverTrigger
                className="p-1 pb-0 rounded-sm focus:outline-none focus:bg-gray-25"
                aria-label="more"
              >
                <span className="wm-more-vert"></span>
              </PopoverTrigger>
              <PopoverContent
                className="primary-popover-content wu-primary-nav-popover-left"
                align="start"
                sideOffset={12}
              >
                {wrappedItems}
              </PopoverContent>
            </Popover>
          )}
        </div>

        {children && (
          <>
            <section
              className="items-center justify-end hidden gap-4 sm:flex wu-primary-nav-right"
              ref={childrenRef}
            >
              {children}
            </section>

            <Popover>
              <PopoverTrigger className="sm:hidden">
                <span className="text-lg wm-menu"></span>
              </PopoverTrigger>
              <PopoverContent
                className="bg-white border border-blue-p w-fit wu-primary-nav-popover-right"
                align="end"
                sideOffset={4}
              >
                {children}
              </PopoverContent>
            </Popover>
          </>
        )}
      </header>
    </>
  )
}
