import {ToggleGroup, ToggleGroupItem} from '@/base/ui/toggleGroup'
import {cn} from '@/lib/utils'
import React from 'react'
import {IWuSwitcherOptions} from '../types/IWuSwitcherOptions'

interface IIconTabProps<T> {
  value: string
  options: IWuSwitcherOptions<T>
  onChange: (e: string) => void
  disabled?: boolean
  dir?: 'ltr' | 'rtl'
}
export const _IconTab = React.forwardRef(
  <T,>(
    props: IIconTabProps<T>,
    ref: React.Ref<HTMLDivElement>,
  ): React.JSX.Element => {
    const {options, value, disabled, dir, onChange} = props

    const itemClass = (itemValue: string): string =>
      cn(
        'w-8 h-8 text-xl leading-5 rounded-full text-gray-lead relative z-10 p-0 flex items-center justify-center',
        disabled && 'cursor-not-allowed data-[state=on]:text-gray-lead',
        itemValue === value && 'text-white',
        disabled && value === itemValue && 'text-gray-lead',
        'wu-switcher-icon-tab-item',
      )

    return (
      <>
        <ToggleGroup
          ref={ref}
          type="single"
          value={value}
          onValueChange={onChange}
          disabled={disabled}
          dir={dir}
          className={cn(
            'relative group flex flex-row items-start justify-start gap-0 w-16 bg-gray-25 rounded-full',
            disabled && 'cursor-not-allowed',
            'wu-switcher-icon-tab-group',
          )}
        >
          <div
            role="slider"
            className={cn(
              'absolute z-0 w-8 h-8 bg-blue-p rounded-full transition-transform duration-300 ease-in-out',
              value === 'opt1' && '',
              value === 'opt2' && dir !== 'rtl' && 'translate-x-full',
              value === 'opt2' && dir === 'rtl' && '-translate-x-full',
              disabled && 'bg-gray-40',
              'wu-switcher-icon-tab-slider',
            )}
          ></div>
          <ToggleGroupItem value="opt1" className={itemClass('opt1')}>
            {options[0].label}
          </ToggleGroupItem>
          <ToggleGroupItem value="opt2" className={itemClass('opt2')}>
            {options[1].label}
          </ToggleGroupItem>
        </ToggleGroup>
      </>
    )
  },
)

_IconTab.displayName = '_IconTab'
