import {WuTooltip} from '@/components/tooltip'
import React, {useEffect, useMemo, useState} from 'react'

export const WuRenderTags: React.FC<{
  tags: string[]
  triggerRef: React.RefObject<HTMLButtonElement | null>
  dir?: 'ltr' | 'rtl'
}> = ({tags, triggerRef, dir}) => {
  const [displayedTags, setDisplayedTags] = useState<string[]>([])
  const [remainingTags, setRemainingTags] = useState<string[]>([])

  const renderRemainingTagsTooltip = useMemo(
    () => (
      <ul className="flex flex-col items-start justify-center list-none">
        {remainingTags.map((tag, index) => (
          <li key={index} className="py-1 text-xs text-left">
            {tag}
          </li>
        ))}
      </ul>
    ),
    [remainingTags],
  )

  const measureTagWidth = (tag: string): number => {
    const tempTag = document.createElement('div')
    Object.assign(tempTag.style, {
      display: 'inline-block',
      visibility: 'hidden',
      position: 'absolute',
      whiteSpace: 'nowrap',
    })
    tempTag.textContent = tag
    document.body.appendChild(tempTag)
    const width = tempTag.offsetWidth
    document.body.removeChild(tempTag)
    return width + 8
  }

  useEffect(() => {
    const calculateTagDisplay = (): void => {
      if (!triggerRef.current) return

      const dropdownButtonWidth = 20 // Fixed space for dropdown button
      const paddingWidth = 36 // Padding or margin in container
      const gap = 8 // Gap between tags
      const containerWidth =
        triggerRef.current.offsetWidth - dropdownButtonWidth - paddingWidth

      let currentWidth = 0
      const visibleTags: string[] = []
      const hiddenTags: string[] = []

      tags.forEach(tag => {
        const tagWidth = measureTagWidth(tag)
        if (currentWidth + tagWidth + gap < containerWidth) {
          visibleTags.push(tag)
          currentWidth += tagWidth + gap
        } else {
          hiddenTags.push(tag)
        }
      })
      setDisplayedTags(visibleTags)
      setRemainingTags(hiddenTags)
    }

    calculateTagDisplay()
    window.addEventListener('resize', calculateTagDisplay)

    return (): void => {
      window.removeEventListener('resize', calculateTagDisplay)
    }
  }, [tags, triggerRef])

  return (
    <div className="flex items-center gap-2 text-xs">
      {displayedTags.map((tag, index) => (
        <span
          key={index}
          className="px-1 py-0.5 text-xs rounded-sm bg-gray-10 wu-combobox-tag"
        >
          {tag}
        </span>
      ))}
      {remainingTags.length > 0 && (
        <WuTooltip
          content={renderRemainingTagsTooltip}
          position={dir === 'rtl' ? 'left' : 'right'}
          className="z-[51] text-left"
        >
          <span className="px-1 py-0.5 text-xs rounded-sm bg-gray-10 wu-combobox-tag">
            +{remainingTags.length}
          </span>
        </WuTooltip>
      )}
    </div>
  )
}
