import {
  DropdownMenuGroup,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
} from '@/base/ui/dropdownMenu'
import {WuCheckbox} from '@/components/checkbox'
import {cn} from '@/lib/utils'
import {
  IWuSelectGroupItem,
  IWuSelectItem,
  IWuSelectOption,
} from '../types/IWuSelectOptions'
import {IWuSelectItemProps, WuSelectItem} from '../ui/_item'
import {IWuSelectProps} from '../WuSelect'

type IRenderSelectItemsProps<T> = Omit<IWuSelectItemProps<T>, 'option'> & {
  option: IWuSelectOption<T>
  selectAll?: IWuSelectProps<T>['selectAll']
  handleSelectAll?: () => void
  isCheckedAll?: boolean
  isPartialSelected?: boolean
}
export const RenderItems = <T,>(
  props: IRenderSelectItemsProps<T>,
): React.JSX.Element => {
  const {
    option,
    selectAll,
    handleSelectAll,
    isCheckedAll,
    isPartialSelected,
    ...rest
  } = props
  const type = 'type' in option ? (option.type as string) : 'option'

  switch (type) {
    case 'all':
      return (
        <DropdownMenuItem
          onSelect={event => {
            event.stopPropagation()
            event.preventDefault()
            handleSelectAll?.()
          }}
          className={cn('cursor-pointer text-xs h-8 py-0 wu-select-item')}
          dir={rest.dir}
        >
          <WuCheckbox
            checked={isCheckedAll || isPartialSelected}
            partial={isPartialSelected}
            label={selectAll?.label || 'Select All'}
          />
        </DropdownMenuItem>
      )
    case 'group':
      return (
        <DropdownMenuGroup dir={rest.dir}>
          <DropdownMenuLabel className="text-xs">
            {(option as IWuSelectGroupItem<T>).label}
          </DropdownMenuLabel>
          {(option as IWuSelectGroupItem<T>).options.map((opt, i) => (
            <WuSelectItem {...rest} option={opt as IWuSelectItem<T>} key={i} />
          ))}
        </DropdownMenuGroup>
      )
    case 'divider':
      return <DropdownMenuSeparator className="mx-4" />
    default:
      return <WuSelectItem {...rest} option={option as IWuSelectItem<T>} />
  }
}
