import {useToast} from '@/base/ui/useToast'
import {IWuToastOptions} from '../types/IWuToastOptions'

export const useWuShowToast = (): {
  showToast: (options: IWuToastOptions) => void
} => {
  const {toast} = useToast()

  if (!toast) {
    throw new Error(
      'Toast not initialized. Please render <WuToast /> at the root of your app.',
    )
  }
  return {
    showToast: ({
      variant = 'success',
      message,
      duration = 3000,
    }: IWuToastOptions): void => {
      if (toast) {
        toast({description: message, variant, duration})
      } else {
        console.error('Toast instance not initialized')
      }
    },
  }
}
