import {ToggleGroup, ToggleGroupItem} from '@/base/ui/toggleGroup'
import {cn} from '@/lib/utils'
import React from 'react'
import {IWuSwitcherOptions} from '../types/IWuSwitcherOptions'

interface ITabProps<T> {
  value: string
  options: IWuSwitcherOptions<T>
  onChange: (e: string) => void
  disabled?: boolean
  dir?: 'ltr' | 'rtl'
  size?: 'sm' | 'md' | 'lg'
}
export const _Tab = React.forwardRef(
  <T,>(
    props: ITabProps<T>,
    ref: React.Ref<HTMLDivElement>,
  ): React.JSX.Element => {
    const {options, value, disabled, dir, size = 'md', onChange} = props

    const itemClass = (itemValue: string): string =>
      cn(
        'w-1/2 text-xs rounded-full text-gray-lead relative z-10 font-normal',
        disabled && 'cursor-not-allowed data-[state=on]:text-gray-lead',
        size === 'sm' && 'h-6 min-w-20',
        size === 'md' && 'h-8 min-w-28',
        size === 'lg' && 'h-10 min-w-36',
        itemValue === value && 'text-white font-medium',
        disabled && value === itemValue && 'text-gray-lead',
        'wu-switcher-tab-item',
      )

    return (
      <>
        <ToggleGroup
          ref={ref}
          type="single"
          value={value}
          onValueChange={onChange}
          disabled={disabled}
          dir={dir}
          className={cn(
            'relative group flex flex-row items-start justify-start gap-0 w-fit bg-gray-25 rounded-full',
            disabled && 'cursor-not-allowed',
            'wu-switcher-tab-group',
          )}
        >
          <div
            role="slider"
            className={cn(
              'absolute z-0 w-1/2 bg-blue-p rounded-full transition-transform duration-300 ease-in-out ',
              size === 'sm' && 'h-6 min-w-20',
              size === 'md' && 'h-8 min-w-28',
              size === 'lg' && 'h-10 min-w-36',
              value === 'opt1' && '',
              value === 'opt2' && dir !== 'rtl' && 'translate-x-full',
              value === 'opt2' && dir === 'rtl' && '-translate-x-full',
              disabled && 'bg-gray-40',
              'wu-switcher-tab-slider',
            )}
          ></div>
          <ToggleGroupItem value="opt1" className={itemClass('opt1')}>
            <div className="min-w-16 max-w-[136px] overflow-hidden text-ellipsis text-nowrap">
              {options[0].label}
            </div>
          </ToggleGroupItem>
          <ToggleGroupItem value="opt2" className={itemClass('opt2')}>
            <div className="min-w-16 max-w-[136px] overflow-hidden text-ellipsis text-nowrap">
              {options[1].label}
            </div>
          </ToggleGroupItem>
        </ToggleGroup>
      </>
    )
  },
)

_Tab.displayName = '_Tab'
