import {useEffect, useState} from 'react'
import type {IToastActionElement, IToastProps} from './toast'

const TOAST_LIMIT = 1
const TOAST_REMOVE_DELAY = 1000000

type IToasterToast = IToastProps & {
  id: string
  title?: React.ReactNode
  description?: React.ReactNode
  action?: IToastActionElement
}

const _actionTypes = {
  ADD_TOAST: 'ADD_TOAST',
  UPDATE_TOAST: 'UPDATE_TOAST',
  DISMISS_TOAST: 'DISMISS_TOAST',
  REMOVE_TOAST: 'REMOVE_TOAST',
} as const

let count = 0

function genId(): string {
  count = (count + 1) % Number.MAX_SAFE_INTEGER
  return count.toString()
}

type IActionType = typeof _actionTypes

type IAction =
  | {
      type: IActionType['ADD_TOAST']
      toast: IToasterToast
    }
  | {
      type: IActionType['UPDATE_TOAST']
      toast: Partial<IToasterToast>
    }
  | {
      type: IActionType['DISMISS_TOAST']
      toastId?: IToasterToast['id']
    }
  | {
      type: IActionType['REMOVE_TOAST']
      toastId?: IToasterToast['id']
    }

interface IState {
  toasts: IToasterToast[]
}

const toastTimeouts = new Map<string, ReturnType<typeof setTimeout>>()

const addToRemoveQueue = (toastId: string): void => {
  if (toastTimeouts.has(toastId)) {
    return
  }

  const timeout = setTimeout(() => {
    toastTimeouts.delete(toastId)
    dispatch({
      type: 'REMOVE_TOAST',
      toastId: toastId,
    })
  }, TOAST_REMOVE_DELAY)

  toastTimeouts.set(toastId, timeout)
}

export const reducer = (state: IState, action: IAction): IState => {
  switch (action.type) {
    case 'ADD_TOAST':
      return {
        ...state,
        toasts: [action.toast, ...state.toasts].slice(0, TOAST_LIMIT),
      }

    case 'UPDATE_TOAST':
      return {
        ...state,
        toasts: state.toasts.map(t =>
          t.id === action.toast.id ? {...t, ...action.toast} : t,
        ),
      }

    case 'DISMISS_TOAST': {
      const {toastId} = action

      // ! Side effects ! - This could be extracted into a dismissToast() action,
      // but I'll keep it here for simplicity
      if (toastId) {
        addToRemoveQueue(toastId)
      } else {
        state.toasts.forEach(toast => {
          addToRemoveQueue(toast.id)
        })
      }

      return {
        ...state,
        toasts: state.toasts.map(t =>
          t.id === toastId || toastId === undefined
            ? {
                ...t,
                open: false,
              }
            : t,
        ),
      }
    }
    case 'REMOVE_TOAST':
      if (action.toastId === undefined) {
        return {
          ...state,
          toasts: [],
        }
      }
      return {
        ...state,
        toasts: state.toasts.filter(t => t.id !== action.toastId),
      }
  }
}

const listeners: Array<(state: IState) => void> = []

let memoryState: IState = {toasts: []}

function dispatch(action: IAction): void {
  memoryState = reducer(memoryState, action)
  listeners.forEach(listener => {
    listener(memoryState)
  })
}

type IToast = Omit<IToasterToast, 'id'>

function toast({...props}: IToast): unknown {
  const id = genId()

  const update = (props: IToasterToast): void =>
    dispatch({
      type: 'UPDATE_TOAST',
      toast: {...props, id},
    })
  const dismiss = (): void => dispatch({type: 'DISMISS_TOAST', toastId: id})

  dispatch({
    type: 'ADD_TOAST',
    toast: {
      ...props,
      id,
      open: true,
      onOpenChange: open => {
        if (!open) dismiss()
      },
    },
  })

  return {
    id: id,
    dismiss,
    update,
  }
}

function useToast(): {
  toasts: IToasterToast[]
  toast: typeof toast
  dismiss: (toastId?: string) => void
} {
  const [state, setState] = useState<IState>(memoryState)

  useEffect(() => {
    listeners.push(setState)
    return (): void => {
      const index = listeners.indexOf(setState)
      if (index > -1) {
        listeners.splice(index, 1)
      }
    }
  }, [state])

  return {
    ...state,
    toast,
    dismiss: (toastId?: string) => dispatch({type: 'DISMISS_TOAST', toastId}),
  }
}

export {toast, useToast}
