import {Popover, PopoverContent, PopoverTrigger} from '@/base/ui/popover'
import {useResizeList} from '@/hooks/useResizeList'
import {cn} from '@/lib/utils'
import React, {useMemo} from 'react'
import './WuSecondaryNavbar.css'

interface INavLinkProps extends React.AnchorHTMLAttributes<HTMLAnchorElement> {
  active?: boolean
  children: React.ReactNode
}
interface INavImageProps extends React.ImgHTMLAttributes<HTMLImageElement> {
  className?: string
}
export interface INavLinkNode {
  link: React.ReactElement<INavLinkProps>
  imgOrIcon: React.ReactElement<INavImageProps>
}

export interface IWuSecondaryNavbarProps
  extends React.HTMLAttributes<HTMLDivElement> {
  Links: INavLinkNode[]
  children?: React.ReactNode
}

export const WuSecondaryNavbar: React.FC<IWuSecondaryNavbarProps> = props => {
  const {Links, children, className, ...rest} = props
  const mod = useMemo(() => {
    return Links.map((n, i) => {
      return React.cloneElement(n.link, {
        key: i,
        className: cn(
          'secondary-link',
          n.link.props.className,
          'wu-secondary-nav-link-container',
        ),
        children: (
          <>
            {React.cloneElement(n.imgOrIcon, {
              className: cn(
                'w-5 h-5 flex items-center justify-center text-lg leading-5',
                n.imgOrIcon.props.className,
              ),
            })}
            <div className="wu-secondary-nav-link">{n.link.props.children}</div>
          </>
        ),
      })
    })
  }, [Links])

  const headerRef = React.useRef<HTMLDivElement>(null)
  const navRef = React.useRef<HTMLDivElement>(null)
  const childrenRef = React.useRef<HTMLDivElement>(null)

  const [openItems, wrappedItems] = useResizeList(mod, headerRef, childrenRef)

  return (
    <>
      <header
        className={cn(
          'flex items-center justify-between gap-4 border-y border-gray-25 h-[50px]',
          className,
        )}
        {...rest}
        ref={headerRef}
      >
        <div className="flex items-center justify-start gap-2">
          <nav
            className="flex items-center justify-start w-full h-full secondary-nav wu-secondary-nav-left"
            ref={navRef}
          >
            {openItems}
          </nav>
          {wrappedItems.length > 0 && (
            <Popover>
              <PopoverTrigger
                className="p-1 pb-0 rounded-sm focus:outline-none focus:bg-gray-25"
                aria-label="more"
              >
                <span className="wm-more-vert"></span>
              </PopoverTrigger>
              <PopoverContent
                className="secondary-popover-content wu-secondary-nav-popover-left"
                align="start"
                sideOffset={12}
              >
                {wrappedItems}
              </PopoverContent>
            </Popover>
          )}
        </div>
        {children && (
          <>
            <div
              className="items-center justify-end hidden sm:flex"
              ref={childrenRef}
            >
              {children}
            </div>
            <Popover>
              <PopoverTrigger className="sm:hidden">
                <span className="text-lg wm-menu"></span>
              </PopoverTrigger>
              <PopoverContent
                className="bg-white border border-blue-p w-fit wu-secondary-nav-popover-right"
                align="end"
                sideOffset={12}
              >
                {children}
              </PopoverContent>
            </Popover>
          </>
        )}
      </header>
    </>
  )
}
