import {WuButton} from '@/components/button'
import {PopoverClose} from '@radix-ui/react-popover'

interface IWuDatePickerFooterProps {
  handleDateChange: () => void
  onReset?: () => void
  disabled?: boolean
  showResetButton?: boolean
}
export const WuDatePickerFooter: React.FC<IWuDatePickerFooterProps> = props => {
  const {handleDateChange, disabled, onReset, showResetButton} = props

  return (
    <div className="flex items-center justify-end gap-2 mt-2 wu-date-picker-footer">
      {showResetButton && (
        <WuButton
          variant="secondary"
          onClick={onReset}
          Icon={
            <span className="text-[20px] leading-5 -mt-[1px] wm-refresh"></span>
          }
          className="wu-date-picker-footer-reset"
        >
          Reset
        </WuButton>
      )}
      <PopoverClose asChild>
        <WuButton
          onClick={handleDateChange}
          disabled={disabled}
          className="wu-date-picker-apply"
        >
          Apply
        </WuButton>
      </PopoverClose>
    </div>
  )
}
