import {DropdownMenuTrigger} from '@/base/ui/dropdownMenu'
import {cn} from '@/lib/utils'
import React, {useEffect, useState} from 'react'
import {IWuSelectProps} from '../WuSelect'

export interface IWuSelectTriggerProps<T> {
  placeholder: string
  value: T | T[]
  multiple?: boolean
  variant?: 'flat' | 'outlined'
  dir?: 'ltr' | 'rtl'
  accessorKey: {value: string; label: string}
  customTriggerChildren?: React.ReactNode
  id?: string
  disabled?: boolean
  selectAll?: IWuSelectProps<T>['selectAll']
  isCheckedAll?: boolean
  width?: number
}

export const WuSelectTrigger = <T,>({
  placeholder,
  value,
  multiple = false,
  accessorKey,
  variant = 'flat',
  dir = 'ltr',
  customTriggerChildren,
  id,
  disabled,
  selectAll,
  isCheckedAll,
  width,
}: IWuSelectTriggerProps<T>): React.JSX.Element => {
  const [label, setLabel] = useState<string>(placeholder)

  useEffect(() => {
    if (!value || (Array.isArray(value) && value.length === 0)) {
      setLabel(placeholder)
      return
    }

    const key = accessorKey.label as keyof T
    if (multiple && Array.isArray(value)) {
      const labels = value.map(item => item[key]).join(', ')
      setLabel(labels)
    } else if (!multiple && !Array.isArray(value)) {
      setLabel(value[key] as string)
    }
  }, [value, accessorKey.label, multiple, placeholder])

  return (
    <DropdownMenuTrigger asChild dir={dir} id={id} disabled={disabled}>
      <button
        disabled={disabled}
        className={cn(
          'flex gap-2 w-full items-center justify-between py-1.5 px-2 text-xs transition-colors cursor-pointer border-b bg-white border-gray-subtle h-8',
          'focus:outline-none focus-visible:border-blue-p rounded-t-sm',
          'disabled:cursor-not-allowed disabled:hover:border-gray-subtle disabled:bg-gray-10 disabled:opacity-50',
          'hover:bg-gray-10 hover:border-blue-p',
          variant === 'outlined' && 'border border-gray-40',
          'group wu-select-trigger',
        )}
      >
        {customTriggerChildren ?? (
          <span
            className="overflow-hidden text-xs text-start text-ellipsis whitespace-nowrap"
            style={{width: `${(width || 0) - 32}px`}}
          >
            {isCheckedAll ? (selectAll?.triggerText ?? label) : label}
          </span>
        )}
        <span className="wm-arrow-drop-down group-data-[state=open]:rotate-180 transition-transform duration-300 text-base"></span>
      </button>
    </DropdownMenuTrigger>
  )
}
