import {DropdownMenuItem} from '@/base/ui/dropdownMenu'
import {WuCheckbox} from '@/components/checkbox'
import {cn} from '@/lib/utils'
import React, {useEffect, useState} from 'react'
import {IWuSelectItem} from '../types/IWuSelectOptions'

export interface IWuSelectItemProps<T> {
  option: IWuSelectItem<T>
  accessorKey: {value: string; label: string}
  value: unknown | unknown[]
  multiple?: boolean
  onSelect: (value: IWuSelectItem<T>) => void
  dir: 'ltr' | 'rtl'
}

export const WuSelectItem = <T,>({
  option,
  value,
  multiple = false,
  onSelect,
  accessorKey,
  dir = 'ltr',
}: IWuSelectItemProps<T>): React.JSX.Element => {
  const [selected, setSelected] = useState<string | string[]>(
    multiple ? [] : '',
  )

  useEffect(() => {
    if (value) {
      if (multiple) {
        if (!Array.isArray(value)) throw new Error('Value must be an array')
        const temp = value as unknown[]
        setSelected(
          temp.map(item =>
            JSON.stringify(
              (item as IWuSelectItem<T>)[accessorKey.value as keyof T],
            ),
          ),
        )
      } else {
        setSelected(
          JSON.stringify(
            (value as IWuSelectItem<T>)[accessorKey.value as keyof T],
          ),
        )
      }
    }
  }, [value, accessorKey.value, multiple])

  const isSelected = multiple
    ? (selected as string[]).includes(
        JSON.stringify(option[accessorKey.value as keyof T]),
      )
    : selected === JSON.stringify(option[accessorKey.value as keyof T])

  const handleClick = (e: React.MouseEvent): void => {
    if (!option.disabled) {
      onSelect(option)
      if (multiple) e.preventDefault()
    }
  }

  return (
    <DropdownMenuItem
      onClick={handleClick}
      disabled={option.disabled}
      className={cn(
        'cursor-pointer wu-select-item h-8 text-xs',
        isSelected && 'bg-gray-10',
      )}
      dir={dir}
    >
      {multiple && <WuCheckbox checked={isSelected} />}
      <ItemChildren option={option} accessorKey={accessorKey} />
    </DropdownMenuItem>
  )
}

export const ItemChildren = <T,>({
  option,
  accessorKey,
}: Pick<
  IWuSelectItemProps<T>,
  'option' | 'accessorKey'
>): React.JSX.Element => {
  const label = String(option[accessorKey.label as keyof T])
  return (
    <div className="flex items-center w-full gap-1 text-xs pl-2">
      {option.icon}
      {label}
      {option.isAdmin && (
        <span className="ml-auto wm-lock text-sm text-orange"></span>
      )}
    </div>
  )
}
