import {format} from 'date-fns'
import React, {useCallback, useEffect, useState} from 'react'

import {Label} from '@/base/ui/label'
import {Popover, PopoverContent} from '@/base/ui/popover'
import {cn} from '@/lib/utils'
import {WuCalender} from '../../WuCalender'
import {WuDatePickerFooter} from '../../ui/_footer'
import {WuDatePickerTrigger} from '../../ui/_trigger'
import {disabledDate, parsedDate} from '../../utils'

export type IWuDatePickerProps = {
  formatString?: string
  value?: Date | string
  disabled?: boolean
  minDate?: Date
  maxDate?: Date
  className?: string
  dir?: 'ltr' | 'rtl'
  onChange?: (date?: Date) => void
  label?: string
  labelPosition?: 'top' | 'left'
  onReset?: () => void
  showResetButton?: boolean
  placeholder?: string
}

export const WuDatePicker: React.FC<IWuDatePickerProps> = ({
  formatString = 'yyyy-MM-dd',
  value,
  onChange,
  disabled,
  minDate,
  maxDate,
  className,
  dir = 'ltr',
  label,
  labelPosition = 'top',
  onReset,
  showResetButton = false,
  placeholder = 'Select Date',
}) => {
  const [date, setDate] = useState<Date | undefined>()
  const [formatted, setFormatted] = useState<string>('')

  // Handles date changes and updates formatted string
  const handleDateChange = useCallback(() => {
    const parsed = parsedDate(date)
    setFormatted(parsed ? format(parsed, formatString) : '')
    if (onChange) onChange(date)
  }, [date, formatString, onChange])

  // Resets the date picker state
  const handleReset = useCallback(() => {
    if (onReset) {
      onReset()
    } else {
      setDate(undefined)
      setFormatted('')
      if (onChange) onChange(undefined)
    }
  }, [onChange, onReset])

  // Updates state when value prop changes
  useEffect(() => {
    const parsed = parsedDate(value)
    setDate(parsed)
    setFormatted(parsed ? format(parsed, formatString) : '')
  }, [value, formatString])

  return (
    <Label
      className={cn(
        'flex gap-1',
        labelPosition === 'left' ? 'items-center' : 'flex-col items-start',
        'wu-date-picker-label',
      )}
      dir={dir}
    >
      {label}
      <Popover>
        <WuDatePickerTrigger
          formattedString={formatted}
          dir={dir}
          placeholder={placeholder}
        />
        <PopoverContent
          className="p-0 w-fit wu-date-picker-content"
          align="start"
          sideOffset={-0.5}
          dir={dir}
        >
          <WuCalender
            dir={dir}
            mode="single"
            captionLayout="dropdown"
            selected={date}
            onSelect={setDate}
            disabled={d => disabledDate(d, disabled, minDate, maxDate)}
            startMonth={minDate}
            endMonth={maxDate}
            required
            autoFocus
            defaultMonth={date}
            className={cn('border border-blue-p', className)}
            footer={
              <WuDatePickerFooter
                handleDateChange={handleDateChange}
                disabled={disabled}
                onReset={handleReset}
                showResetButton={showResetButton}
              />
            }
          />
        </PopoverContent>
      </Popover>
    </Label>
  )
}
