import React from 'react'

export const useResizeContent = (
  parentRef: React.RefObject<HTMLDivElement | null>,
  labelWidth: number = 0,
): number => {
  const [contentWidth, setContentWidth] = React.useState<number>(200)

  React.useEffect(() => {
    const updateContentWidth = (): void => {
      if (parentRef.current) {
        const parentWidth = parentRef.current.offsetWidth
        setContentWidth(parentWidth - labelWidth)
      }
    }

    updateContentWidth()

    window.addEventListener('resize', updateContentWidth)
    return (): void => window.removeEventListener('resize', updateContentWidth)
  }, [parentRef, labelWidth])

  return contentWidth
}
